import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { MdAlertComponent } from 'src/app/pages/modais/md-alert/md-alert.component';
import { ModalService } from 'src/app/services/modal.service';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FooterComponent implements OnInit {
  public layout = environment.layout;

  constructor(private modalS: ModalService) { }

  ngOnInit() {
  }

  alertWhats() {
    let numeroWhats = '+55 41 3091-0403';
    if (this.layout === 'esalpet') {
      numeroWhats = '+55 41 3091-0400';
    }
    this.modalS.createModal(MdAlertComponent, { title: 'Whatsapp', message: numeroWhats });
  }

  scrollTop() {
    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
  }

  notifyEnvioNewsLetter() {
    setTimeout(() => {
      (document.getElementById('name') as HTMLInputElement).value = '';
      (document.getElementById('email') as HTMLInputElement).value = '';
    }, 1000);
  }
}
