import { Component, OnInit } from '@angular/core';
import { CartService } from './../../services/cart.service';
import { UserService } from './../../services/user.service';
import { StoreService } from 'src/app/services/store.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { MdImageComponent } from 'src/app/pages/modais/md-image/md-image.component';
import { MdConfirmComponent } from '../modais/md-confirm/md-confirm.component';
import { TrustvoxService } from 'src/app/services/trustvox.service';
import { MdAlertComponent } from '../modais/md-alert/md-alert.component';
import { HttpClient } from '@angular/common/http';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { environment } from 'src/environments/environment';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-produto',
  templateUrl: './produto.component.html',
  styleUrls: ['./produto.component.scss']
})
export class ProdutoComponent implements OnInit {
  public layout = environment.layout;

  public item: any = undefined;

  public itemId;
  public noItem = true;
  public medidas = [];
  public quantidade = 1;
  public fotoAtual = 0;
  public loading = true;
  public data: Date = new Date();

  constructor(
    public userS: UserService,
    public cartS: CartService,
    public storeS: StoreService,
    private activRoute: ActivatedRoute,
    private modalS: ModalService,
    private router: Router,
    private tvS: TrustvoxService,
    private http: HttpClient,
    private gTagS: GoogleAnalyticsService,
    private titleService: Title
  ) {  }

  ngOnInit() {
    // const teste = {
    //   order_id: 1342432432432432423423,
    //   delivery_date: '2020-02-02T14:26:40+00:00',
    //   client: {
    //       first_name: 'John',
    //       last_name: 'Buyer',
    //       phone_number: 5541994163481,
    //       email: 'gustavosk22@hotmail.com'
    //   },
    //   items: [
    //       {
    //           name: 'Buque Especial de Rosas Vermelhas C20',
    //           id: 7518,
    //           url: 'http://localhost:4200/produto/buque-especial-de-rosas-vermelhas-c20?id=7518',
    //           price: 234.90
    //       }
    //   ],
    //   seller: {
    //       id: 110659,
    //       name: 'Esalflores'
    //   }
    // };

    // ErpInterceptor.useErpHeader = false;
    // this.http.post('https://trustvox.com.br/api/stores/110659/orders', teste, { headers: { Authorization: 'token sZK-ZRicfNPZnCixMLfG', 'Content-Type': 'application/json', Accept: 'application/vnd.trustvox.com; version=1'} }).subscribe(res => {
    //   console.log(res);
    // });
    this.activRoute.queryParams.subscribe(params => {
      this.loading = true;
      if (!params.id) {
        return;
      }

      this.itemId = params.id;
      setTimeout(() => {
        this.storeS.getItemById(this.itemId).subscribe((resItem: any) => {
          this.item = resItem;
          if (!this.item?.tabela?.vVenda) {
            this.loading = false;
            return;
          }
          this.loading = false;
          if (resItem.error) {
            this.modalS.createModal(MdAlertComponent, { title: 'Produto não encontrado.', message: resItem.error });
            this.router.navigate(['/']);
            return;
          }

          if (!this.item.item_linkWeb) {
            throw new Error('Item não cadastrado corretamente, verifique a propriedade item_linkWeb');
          }
          this.titleService.setTitle(this.item.descricao);

          setTimeout(() => {
            this.setDescricao();
            // this.tvS.configTrustvox(this.item);
          });

          this.noItem = false;
          this.item.quantidade = 1;
          this.gTagS.viewItem(this.item);
        });

      }, 2000);

    });

  }

  setDescricao() {
    document.getElementById('descricao').innerHTML = this.item.item_linkWeb?.linkWeb.descricaoHtml;
  }

  addToCart() {
    this.modalS.createModal(MdConfirmComponent, { title: 'Adicionar ao carrinho.', message: 'Deseja adicionar este item ao carrinho?' }).then(res => {
      if (res) {
        this.item.quantidade = this.quantidade;
        this.item.descricao = this.item.item_linkWeb.linkWeb.titulo;
        this.cartS.addItem(this.item);
        this.gTagS.addToCart(this.item);
        this.router.navigate(['/carrinho']);
      }
    });
  }

  updateQuantidade(value: string) {
    this.quantidade = Number(value);
    Number(value) > this.item.estoque.estAtual ? this.item.quantidade = this.item.estoque.estAtual : this.item.quantidade = Number(value);
  }

  openMdImage(url: string) {
    this.modalS.createModal(MdImageComponent, { url });
  }

  changeFoto(index: number) {
    this.fotoAtual = index;
  }
}
