import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { mustMatch } from 'src/app/core/Functions';
import { ModalService } from 'src/app/services/modal.service';
import { UserService } from 'src/app/services/user.service';
import { MdAlertComponent } from '../modais/md-alert/md-alert.component';

@Component({
  selector: 'app-redefinir-senha',
  templateUrl: './redefinir-senha.component.html',
  styleUrls: ['./redefinir-senha.component.scss']
})
export class RedefinirSenhaComponent implements OnInit {
  code: string;
  formRedefinirSenha: FormGroup;

  constructor(private activeRoute: ActivatedRoute,
              private router: Router,
              private fb: FormBuilder,
              private userS: UserService,
              private modalS: ModalService) { }

  ngOnInit(): void {
    this.activeRoute.params.subscribe(params => {
      if (!params.code) {
        this.router.navigate(['/']);
      }
      this.code = params.code;
    });

    this.setupForm();
  }

  setupForm() {
    this.formRedefinirSenha = this.fb.group({
      accessCode: [this.code, [Validators.required, Validators.minLength(8), Validators.maxLength(8)]],
      password: [null, [Validators.minLength(4), Validators.required]],
      confirmPassword: [null, [Validators.minLength(4), Validators.required]]
    },
    {
      validators: [mustMatch('password', 'confirmPassword')]
    }
    );
  }

  get form() {
    return this.formRedefinirSenha.controls;
  }

  redefinirSenha($event: any) {
    this.userS.redefinirSenha(this.formRedefinirSenha.value, $event).subscribe((res: any) => {
      if (res.error) {
        this.modalS.createModal(MdAlertComponent, {title: 'Erro ao criar nova senha.', message: res.error});
        return;
      }

      this.modalS.createModal(MdAlertComponent, {title: 'Redefinição de senha.', message: 'Senha alterada com sucesso!'});
      this.router.navigate(['/login']);
    });
  }
}
