import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-md-regiao',
  templateUrl: './md-regiao.component.html',
  styleUrls: ['./md-regiao.component.scss']
})
export class MdRegiaoComponent implements OnInit {

  public data: any;
  public modal: any;
  public resolve: any;
  public layout = environment.layout;

  constructor() { }

  ngOnInit() {
  }

  changeCidade(cidade: string, id: number) {
    this.resolve = { cidade, id };
    this.modal.close();
  }

}
