import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { UserService } from 'src/app/services/user.service';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';
import { MdConfirmComponent } from '../../modais/md-confirm/md-confirm.component';

@Component({
  selector: 'app-minha-conta',
  templateUrl: './minha-conta.component.html',
  styleUrls: ['./minha-conta.component.scss']
})
export class MinhaContaComponent implements OnInit {
  public pedidos = [];

  constructor(public userS: UserService, private readonly pedidoS: PedidoService, private modalS: ModalService, private router: Router) { }

  ngOnInit(): void {
    if (this.userS.user) {
      this.pedidoS.getClientPedidos().subscribe((res: any) => {
        this.pedidos = res;
      });
    } else {
      this.modalS.createModal(MdAlertComponent, {title: 'Cliente não encontrado.', message: 'Cliente não encontrado, favor fazer o login novamente.'});
    }
  }

  excluirConta() {
    this.modalS.createModal(MdConfirmComponent, {title: 'Excluir Conta', message: 'Deseja excluir seu cadastro? Essa ação não é reversível e irá desativar sua conta!'})
    .then(res => {
      if (res) {
        this.userS.excluirConta(this.userS.user.id).subscribe((resExc: any) => {
          if (resExc.error) {
            this.modalS.createModal(MdAlertComponent, {title: 'Excluir Cadastro', message: resExc.error});
            return;
          }
          this.modalS.createModal(MdAlertComponent, {title: 'Excluir Cadastro', message: resExc});
          this.userS.logoff();
        });
      }
    });
  }
}
