import { Component, OnInit } from '@angular/core';
import { CartService } from './../../services/cart.service';
import { UserService } from './../../services/user.service';
import { environment } from '../../../environments/environment';
import { StoreService } from 'src/app/services/store.service';
import { Router } from '@angular/router';
import { slugify } from 'src/app/core/Functions';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  public data: Date = new Date();
  public idOcasiao = 0;
  public hotsite = false;
  public layout = environment.layout;
  public produtosDiasNamorado = [];

  constructor(
    public userS: UserService,
    public cartS: CartService,
    public store: StoreService,
    private router: Router
  ) { }

  ngOnInit() {
    this.hotsite = (localStorage.getItem('hotsite') === 'true') || false;
    this.store.getItensByCateg(393).subscribe((resB: any) => {
      for (let i = 0; i <= 200; i++) {
        if (resB[i]) {
          this.produtosDiasNamorado.push(resB[i]);
        }
      }
      this.produtosDiasNamorado = this.produtosDiasNamorado.filter((produto: any) => {
        return produto.estoque !== null || produto.estoque?.estAtual > 0;
      });
    });
    setTimeout(() => {
      this.verifyProdutoBox();
    }, 8000);
  }

  abrirLista(categoria, id) {
    const slug = slugify(categoria);
    this.router.navigate(['/lista', 'categoria', slug], { queryParams: { id } });
  }

  verifyProdutoBox() {
    const prodBoxes = document.getElementsByClassName('invalid-prod-box');
    // @ts-ignore
    for (const prodBox of prodBoxes) {
      prodBox.classList.add('d-none');
    }
  }

  selectOcasion() {
    if (this.idOcasiao === 0) {
      return;
    }
    this.router.navigate(['/lista', 'categoria', 'ocasiao'], { queryParams: { id: this.idOcasiao } });
  }
}
