import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../services/user.service';
import { mustMatch, listaPaises, validateCpfCnpj } from '../../core/Functions';
import { ModalService } from 'src/app/services/modal.service';
import { MdEsquecisenhaComponent } from '../modais/md-esquecisenha/md-esquecisenha.component';
import { MdAlertComponent } from '../modais/md-alert/md-alert.component';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { environment } from 'src/environments/environment';

declare var grecaptcha: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public redirect = null;
  public frmLogin: FormGroup;
  public frmCadastro: FormGroup;
  public loading = false;
  public loadingReg = false;
  public paises = listaPaises();
  public cpfcnpjValido = true;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder,
    public userS: UserService,
    private modalS: ModalService,
    private gTagS: GoogleAnalyticsService,
    private pedidoS: PedidoService
  ) { }

  ngOnInit() {
    this.setupForms();
    this.redirect = this.activatedRoute.snapshot.queryParams.redirect;
  }

  setupForms() {
    this.frmCadastro = this.fb.group({
      nome: [null, Validators.required],
      email: [null, [Validators.email, Validators.required]],
      pais: ['Brasil', Validators.required],
      cpfcnpj: [null],
      password: [null, [Validators.minLength(4), Validators.required]],
      confirmPassword: [null, [Validators.minLength(4), Validators.required]]
    },
    {
      validators: [mustMatch('password', 'confirmPassword')]
    }
    );

    this.frmLogin = this.fb.group({
      email: [null, Validators.required],
      password: [null, Validators.required],
      token: [null]
    });
  }

  get formCadastro() {
    return this.frmCadastro.controls;
  }

  login($event) {
    if (this.frmLogin.invalid) {
      return;
    }

    this.loading = true;

    try {
      grecaptcha.ready(() => {
        grecaptcha.execute(environment.recaptchaToken, {action: 'LOGIN'}).then((token) => {
          this.frmLogin.patchValue({ token });
          this.userS.login($event, this.frmLogin.value, false).subscribe((res: any) => {
            this.loading = false;
            if (res.error) {
              if (res.error.startsWith('Número de tentativas excedido')) {
                this.modalS.createModal(MdAlertComponent, { title: 'Número de tentativas excedido.', message: res.error });
                return;
              }
              if (res.error.startsWith('Usuário ou senha')) {
                this.modalS.createModal(MdAlertComponent, {title: 'Usuário ou senha inválidos.', message: 'Verifique os dados digitados e tente novamente.'});
                return;
              }
              this.modalS.createModal(MdAlertComponent, { title: 'Erro ao realizar login.', message: res.error });
              return;
            }
            this.userS.user = res.user;
            this.userS.jwt = res.jwt;
            this.pedidoS.checkAbandonedCart();
            this.gTagS.login(res.user.id);
            if (localStorage.getItem('goCheckout') === 'true') {
              this.router.navigate(['/carrinho']);
              return;
            }
            if (localStorage.getItem('goAbandonedCart')) {
              this.router.navigate(['/carrinho/' + localStorage.getItem('goAbandonedCart')]);
              localStorage.removeItem('goAbandonedCart');
              return;
            }
            this.router.navigate(['/']);
          });
        });
      });
    } catch (error) {
      console.error(error);
      this.loading = false;
    }
  }

  logoff() {
    this.userS.user = null;
    this.router.navigate(['/']);
  }

  register($event) {
    this.checkPais();
    if (!this.cpfcnpjValido) {
      this.modalS.createModal(MdAlertComponent, {title: 'CPF/CNPJ Inválido!', message: 'Favor preencher um CPF ou CNPJ válido antes de finalizar o cadastro.'});
      return;
    }
    this.loadingReg = true;
    try {
      grecaptcha.ready(() => {
        grecaptcha.execute(environment.recaptchaToken, {action: 'REGISTER'}).then((token) => {
          this.userS.registerPessoa($event, this.frmCadastro.value).subscribe((res: any) => {
            if (res.data?.error || res.error) {
              this.modalS.createModal(MdAlertComponent, {title: 'Erro ao cadastrar!', message: res.data?.error || res.error});
              this.loadingReg = false;
              return;
            }
            this.userS.login($event, { email: this.frmCadastro.value.email, password: this.frmCadastro.value.password, token }, false).subscribe((resl: any) => {
              this.userS.user = resl.user;
              this.userS.jwt = resl.jwt;
              this.router.navigate(['/cliente/cadastro']);
              this.loadingReg = false;
            });
          });
        });
      });
    } catch (error) {
      this.loadingReg = false;
      this.modalS.createModal(MdAlertComponent, {title: 'Erro ao cadastrar!', message: error.message});
      return;
    }
  }

  checkPais() {
    if (this.frmCadastro.value.pais === 'Brasil') {
      if (!this.frmCadastro.value.cpfcnpj || !validateCpfCnpj(this.frmCadastro.value.cpfcnpj)) {
        this.cpfcnpjValido = false;
        return;
      }
    }
    this.cpfcnpjValido = true;
  }

  esqueciSenha() {
    this.modalS.createModal(MdEsquecisenhaComponent);
  }
}
