import { UserService } from './../../../services/user.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';
import { MdAlterarsenhaComponent } from './../../../pages/modais/md-alterarsenha/md-alterarsenha.component';
import { ModalService } from 'src/app/services/modal.service';
import { Router } from '@angular/router';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';
import { listaPaises, validateCpfCnpj } from 'src/app/core/Functions';

@Component({
  selector: 'app-cadastro',
  templateUrl: './cadastro.component.html',
  styleUrls: ['./cadastro.component.scss']
})
export class CadastroComponent implements OnInit {
  public firstLogin = false;
  public formCadastro: FormGroup;
  public showErrors = false;
  public paises = listaPaises();
  public cpfcnpjValido = true;
  public loading = false;

  constructor(public userS: UserService, public fb: FormBuilder, private http: HttpClient, private modalS: ModalService, private router: Router) { }

  ngOnInit() {
    this.setupForm();
    this.checkForm();
  }

  setupForm() {
    const u = this.userS.user;
    this.formCadastro = this.fb.group({
      email: [u.email, [Validators.email, Validators.required]],
      pais: [u.pais],
      logradouro: [u.logradouro || u.cobrLogradouro, Validators.required],
      cep: [u.cep || u.cobrCep, Validators.required],
      numero: [u.numero || u.cobrNumero, Validators.required],
      complemento: [u.complemento || u.cobrComplemento],
      bairro: [u.bairro || u.cobrBairro, Validators.required],
      uf: [u.uf || u.cobrUf, Validators.required],
      razao: [u.razao, Validators.required],
      cpfcnpj: [u.cpf || u.cnpj],
      nascimento: [u.nascimento, Validators.required],
      fone: [u.fone, Validators.required],
      fone2: [u.fone2],
      id: [u.id],
      municipio: [u.municipio || u.cobrMunicipio, Validators.required],
      receberSMS: [u.receberSMS]
    });
  }

  checkForm() {
    if (this.formCadastro.invalid) {
      this.firstLogin = true;
    }
  }

  get form() {
    return this.formCadastro.controls;
  }

  buscarCep(cep) {
    if (!cep) {
      return;
    }
    ErpInterceptor.useErpHeader = false;
    const validacep = /^[0-9]{8}$/;
    if (validacep.test(cep)) {
      const urlViacep = `https://viacep.com.br/ws/${cep}/json/`;
      this.http.get(urlViacep).subscribe((res: any) => {
        if (res.erro) {
          this.formCadastro.patchValue({cep: ''});
          this.modalS.createModal(MdAlertComponent, {title: 'CEP não encontrado.', message: 'Verifique o CEP digitado e tente novamente.'});
          return;
        } else {
          this.formCadastro.patchValue({
            cep,
            logradouro: res.logradouro,
            bairro: res.bairro,
            municipio: res.localidade,
            uf: res.uf,
            complemento: res.complemento,
            pais: 'Brasil'
          });
          document.getElementById('inputNumero').focus();
        }
      });
    } else {
      this.formCadastro.patchValue({cep: ''});
      this.modalS.createModal(MdAlertComponent, {title: 'CEP inválido!', message: 'Digite um CEP válido.'});
    }
  }

  salvarDados($event) {
    try {
      this.checkPais();
      if (!this.cpfcnpjValido) {
        this.modalS.createModal(MdAlertComponent, {title: 'Formulário Incompleto!', message: 'Favor preencher um CPF ou CNPJ válido antes de salvar os dados.'});
        return;
      }
      if (this.formCadastro.invalid) {
        this.modalS.createModal(MdAlertComponent, {title: 'Formulário Incompleto!', message: 'Por favor, verifique os dados e tente salvar novamente.'});
        this.showErrors = true;
        return;
      }
      if (this.formCadastro.value.cpfcnpj) {
        this.removeNotNumerics();
      }
      this.loading = true;
      this.userS.updatePessoa($event, this.formCadastro.value).subscribe((res: any) => {
        this.checkForm();
        this.userS.user = res;
        if (this.firstLogin === true) {
          this.modalS.createModal(MdAlertComponent, {title: 'Cadastro', message: 'Cadastro concluido com sucesso!'});
          this.router.navigate(['/']);
        }
        else {
          this.modalS.createModal(MdAlertComponent, {title: 'Cadastro', message: 'Cadastro atualizado com sucesso!'});
        }
        this.loading = false;
      });
    } catch (error) {
      this.modalS.createModal(MdAlertComponent, {title: 'Erro ao salvar cadastro.', message: error.message});
      this.loading = false;
    }
  }

  removeNotNumerics() {
    this.formCadastro.patchValue({
      cpfcnpj: this.formCadastro.value.cpfcnpj.replace(/\D/g, '')
    });
  }

  alterarSenha($event) {
    $event.preventDefault();
    this.modalS.createModal(MdAlterarsenhaComponent).then(res => {
    });
  }

  checkPais() {
    if (this.formCadastro.value.pais === 'Brasil') {
      if (!this.formCadastro.value.cpfcnpj || !validateCpfCnpj(this.formCadastro.value.cpfcnpj)) {
        this.cpfcnpjValido = false;
        return;
      }
    }
    this.cpfcnpjValido = true;
  }
}
