import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import localePt from '../../../node_modules/@angular/common/locales/pt';
import localeEn from '../../../node_modules/@angular/common/locales/en';
import localeEs from '../../../node_modules/@angular/common/locales/es';
import { registerLocaleData } from '@angular/common';
import { isPlatformServer } from '@angular/common';

const map = require('../../assets/language.json');

@Injectable({
  providedIn: 'root'
})
export class LanguageService {

  public lang = 'pt';

  public map = map;

  constructor(@Inject(PLATFORM_ID) private platformId) {
    registerLocaleData(localePt, 'pt');
    registerLocaleData(localeEs, 'es');
    registerLocaleData(localeEn, 'en');

    if (isPlatformServer(this.platformId)) { return; }

    const localLang = localStorage.getItem('language');
    if (localLang) { this.lang = localLang; }
  }

  changeLanguage(lang) {
    this.lang = lang;
    localStorage.setItem('language', lang);
    window.location.reload();
  }

}
