import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { slugify } from '../../core/Functions';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';
import { MdConfirmComponent } from 'src/app/pages/modais/md-confirm/md-confirm.component';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-produto-box',
  templateUrl: './produto-box.component.html',
  styleUrls: ['./produto-box.component.scss']
})
export class ProdutoBoxComponent implements OnInit {

  @Input() public item: any;
  public slugItem = '';
  public layout = environment.layout;

  constructor(private router: Router, private cartS: CartService, private modalS: ModalService, private gTagS: GoogleAnalyticsService) {
  }

  ngOnInit(): void {
    if (this.item.item_linkWeb) {
      this.slugItem = slugify(this.item?.item_linkWeb?.linkWeb?.titulo);
    }
  }

  abrirProduto(item, $event: Event) {
    $event.preventDefault();
    this.gTagS.selectItem(item);
    const slug = slugify(item.item_linkWeb.linkWeb.titulo);
    this.router.navigate(['/produto', slug], { queryParams: { id: item.id } });
  }

  adicionarCarrinho(item, $event: Event) {
    $event.stopPropagation();
    $event.preventDefault();
    this.modalS.createModal(MdConfirmComponent, { title: 'Adicionar ao carrinho.', message: 'Deseja adicionar este item ao carrinho?' }).then(res => {
      if (res) {
        item.quantidade = 1;
        item.descricao = item.item_linkWeb.linkWeb.titulo;
        this.gTagS.addToCart(this.item);
        this.cartS.addItem(item);
        this.router.navigate(['/carrinho']);
      }
    });
  }
}
