import { LanguageService } from '../services/language.service';
import { Directive, Input, OnInit, Renderer2, ElementRef } from '@angular/core';

/**
 * <label appLang="HOME.emailCpf">Email ou CPF</label>
 */
@Directive({
  selector: '[appLang]'
})
export class LangDirective implements OnInit {

  @Input() appLang;

  constructor(
    public languageS: LanguageService,
    private renderer: Renderer2,
    private el: ElementRef<HTMLElement>,
  ) {}

  ngOnInit() {
    if (this.languageS.lang === 'pt') { return; }
    const mapped = this.languageS.map[this.appLang];
    if (!mapped) {
      const inner = this.el.nativeElement.innerHTML;
      console.warn(`Mensagem não traduzida %c [${this.appLang}] => "${inner}"`, 'font-weight: bold; background-color: #ecefa9');
      return;
    }
    this.renderer.setProperty(this.el.nativeElement, 'outerHTML',
      this.languageS.map[this.appLang][this.languageS.lang]
    );
  }

}
