import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { btnLoading } from 'src/app/core/Functions';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';

@Component({
  selector: 'app-md-naoseicep',
  templateUrl: './md-naoseicep.component.html',
  styleUrls: ['./md-naoseicep.component.scss']
})
export class MdNaoseicepComponent implements OnInit {
  public modal: any;
  formEnd: FormGroup;
  public endArr = [];
  resolve: any;

  constructor(private fb: FormBuilder, private http: HttpClient) { }

  ngOnInit(): void {
    this.setupForm();
  }

  setupForm() {
    this.formEnd = this.fb.group({
      uf: ['PR', Validators.required],
      cidade: [null, Validators.required],
      logradouro: [null, Validators.required]
    });
  }

  buscarCep($event) {
    if (this.formEnd.invalid) {
      return;
    }
    ErpInterceptor.useErpHeader = false;
    const url = `https://viacep.com.br/ws/${this.formEnd.value.uf}/${this.formEnd.value.cidade}/${this.formEnd.value.logradouro}/json/`;
    this.http.get(url).pipe(btnLoading($event)).subscribe((res: any) => {
      this.endArr = res;
    });
  }

  escolherCep(cep: string) {
    this.resolve = cep;
    this.modal.close();
  }

  cancelar() {
    this.resolve = null;
    this.modal.close();
  }
}
