import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { environment } from 'src/environments/environment';
import { MdAlertComponent } from '../modais/md-alert/md-alert.component';

@Component({
  selector: 'app-newsletter',
  templateUrl: './newsletter.component.html',
  styleUrls: ['./newsletter.component.scss']
})

export class NewsletterComponent implements OnInit {
  public formNewsLetter: FormGroup;
  public showErrors = false;
  public tipoFloresCheckbox: any = [];
  public ocasiaoCheckbox: any = [];
  public estados =
    ['Acre',
      'Alagoas',
      'Amapá',
      'Amazonas',
      'Bahia',
      'Ceará',
      'Distrito Federal',
      'Espirito Santo',
      'Goiás',
      'Maranhão',
      'Mato Grosso do Sul',
      'Mato Grosso',
      'Minas Gerais',
      'Pará',
      'Paraíba',
      'Paraná',
      'Pernambuco',
      'Piauí',
      'Rio de Janeiro',
      'Rio Grande do Norte',
      'Rio Grande do Sul',
      'Rondônia',
      'Roraima',
      'Santa Catarina',
      'São Paulo',
      'Sergipe',
      'Tocantins'];
    public layout = environment.layout;

  constructor(public fb: FormBuilder, private router: Router, private modalS: ModalService) { }

  ngOnInit(): void {
    this.setupForm();
  }

  setupForm() {
    this.formNewsLetter = this.fb.group({
      nome: [null, [Validators.required]],
      email: [null, [Validators.email, Validators.required]],
      uf: [null, [Validators.required]],
      cidade: [null, [Validators.required]],
      nascimento: [null, [Validators.required]],
      genero: [null, [Validators.required]],
      tipoFlores: [null, Validators.required],
      ocasioes: [null, [Validators.required]]
    });

  }

  tipoFloresChange(event) {
    if (event.target.checked) {
      this.tipoFloresCheckbox.push(event.target.value);
    }
  }

  ocasioesChange(event) {
    if (event.target.checked) {
      this.ocasiaoCheckbox.push(event.target.value);
    }
  }

  get form() {
    return this.formNewsLetter.controls;
  }

  selectedEstado(estado) {
  }

  unselectAll() {
    this.formNewsLetter.patchValue({
      tipoFlores: false,
      ocasioes: false,
    });
  }

  submitForm() {
    if (this.formNewsLetter.invalid) {
      this.showErrors = true;
      return;
    }
    else {
      this.formNewsLetter.get('tipoFlores').patchValue(this.tipoFloresCheckbox);
      this.formNewsLetter.get('ocasioes').patchValue(this.ocasiaoCheckbox);
      // fluxo mandar pro back

      this.unselectAll();
      this.router.navigate(['/']);
      this.modalS.createModal(MdAlertComponent, {title: 'Newsletter', message: 'Newsletter criada com sucesso!'});

    }
  }

}
