import { Component, OnInit } from '@angular/core';
import { ModalService } from 'src/app/services/modal.service';
import { UserService } from 'src/app/services/user.service';
import { MdConfirmComponent } from '../../modais/md-confirm/md-confirm.component';
import { MdUpdateenderecoComponent } from '../../modais/md-updateendereco/md-updateendereco.component';

@Component({
  selector: 'app-enderecos',
  templateUrl: './enderecos.component.html',
  styleUrls: ['./enderecos.component.scss']
})
export class EnderecosComponent implements OnInit {

  constructor(public userS: UserService, private modalS: ModalService) { }

  ngOnInit(): void {
  }

  updateEndereco(index: number) {
    this.modalS.createModal(MdUpdateenderecoComponent, { index }).then(() => { });
  }

  excluirEndereco(index: number, $event: any) {
    let form = {};
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar', message: 'Deseja excluir este endereço?' }).then(res => {
      if (res) {
        if (index === 1) {
          form = {
            id: this.userS.user.id,
            logradouro: null,
            numero: null,
            uf: null,
            bairro: null,
            complemento: null,
            municipio: null,
            cep: null
          };
        } else {
          form = {
            id: this.userS.user.id,
            cobrLogradouro: null,
            cobrNumero: null,
            cobrUf: null,
            cobrBairro: null,
            cobrComplemento: null,
            cobrMunicipio: null,
            cobrCep: null
          };
        }
        this.userS.excluirEndereco(form, $event).subscribe((resExc: any) => {
          this.userS.user = resExc;
        });
      }
    });
  }
}
