import { Component, OnInit } from '@angular/core';
import { ModalService } from 'src/app/services/modal.service';
import { UserService } from 'src/app/services/user.service';
import { MdConfirmComponent } from '../md-confirm/md-confirm.component';
import { MdUpdateenderecoComponent } from '../md-updateendereco/md-updateendereco.component';

@Component({
  selector: 'app-md-meusenderecos',
  templateUrl: './md-meusenderecos.component.html',
  styleUrls: ['./md-meusenderecos.component.scss']
})
export class MdMeusenderecosComponent implements OnInit {
  public modal: any;
  private resolve: any = null;
  private end1 = {};
  private end2 = {};
  public endArr = [];

  constructor(private userS: UserService, private modalS: ModalService) { }

  ngOnInit(): void {
    this.setupEnderecos();
  }

  setupEnderecos() {
    this.end1 = {
      logradouro: this.userS.user.logradouro,
      cep: this.userS.user.cep,
      numero: this.userS.user.numero,
      municipio: this.userS.user.municipio,
      uf: this.userS.user.uf,
      complemento: this.userS.user.complemento,
      bairro: this.userS.user.bairro
    };
    this.end2 = {
      logradouro: this.userS.user.cobrLogradouro,
      cep: this.userS.user.cobrCep,
      numero: this.userS.user.cobrNumero,
      municipio: this.userS.user.cobrMunicipio,
      uf: this.userS.user.cobrUf,
      complemento: this.userS.user.cobrComplemento,
      bairro: this.userS.user.cobrBairro
    };
    this.endArr = [this.end1, this.end2];
  }

  escolherModal(endereco: any) {
    this.resolve = endereco;
    this.modal.close();
  }

  excluirEndereco(index: number, $event: any) {
    $event.stopPropagation();
    let form = {};
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar', message: 'Deseja excluir este endereço?' }).then(res => {
      if (res) {
        if (index === 1) {
          form = {
            id: this.userS.user.id,
            logradouro: null,
            numero: null,
            uf: null,
            bairro: null,
            complemento: null,
            municipio: null,
            cep: null
          };
        } else {
          form = {
            id: this.userS.user.id,
            cobrLogradouro: null,
            cobrNumero: null,
            cobrUf: null,
            cobrBairro: null,
            cobrComplemento: null,
            cobrMunicipio: null,
            cobrCep: null
          };
        }
        this.userS.excluirEndereco(form, $event).subscribe((resExc: any) => {
          this.userS.user = resExc;
          this.modal.close();
        });
      }
    });
  }

  updateEndereco(index: number) {
    this.modalS.createModal(MdUpdateenderecoComponent, { index }).then(() => { });
  }
}
