import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment';
import { MdConfirmComponent } from '../pages/modais/md-confirm/md-confirm.component';
import { CartService } from './cart.service';
import { ModalService } from './modal.service';
import { UserService } from './user.service';

@Injectable({
  providedIn: 'root'
})
export class PedidoService {

  constructor(private http: HttpClient, private userS: UserService, private modalS: ModalService, private cartS: CartService, private router: Router) { }

  postPedido(dadosCart, status = 'Pago') {
    return this.http.post(`${environment.API_URL}pedidoWeb/createPedidoWeb`, { dadosCart, status });
  }

  getClientPedidos() {
    return this.http.post(`${environment.API_URL}pedidoWeb/getPedidosByCliente`, {id: this.userS.user.id});
  }

  getById(pedidoWeb_id, pessoa_id) {
    return this.http.post(environment.API_URL + 'pedidoWeb/getById', { pedidoWeb_id, pessoa_id });
  }

  cupomDesconto(cupom: string) {
    return this.http.post(`${environment.API_URL}cupomDesconto`, { cupom });
  }

  checkAbandonedCart() {
    if (localStorage.getItem('abandonedCart') !== 'false') {
      setTimeout(() => {
        if (JSON.parse(localStorage.getItem('user'))) {
          this.getClientPedidos().subscribe((clientPedidos: any) => {
            for (const pedido of clientPedidos) {
              if (pedido.status === 'Abandonado' && !pedido.pago && !pedido.pedido_id) {
                this.modalS.createModal(MdConfirmComponent, { title: 'Recuperar Carrinho?', message: 'Parece que você deixou um carrinho abandonado, deseja recuperar o carrinho?' }).then(res => {
                  if (res) {
                    this.cartS.entrega = pedido.json.entrega;
                    this.cartS.item = pedido.json.item;
                    this.router.navigate(['/carrinho']);
                  } else {
                    localStorage.setItem('abandonedCart', 'false');
                  }
                });
              }
            }
          });
        }
      }, 5000);
    }
  }
}
