import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { btnLoading } from 'src/app/core/Functions';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class BancobrasilService {

  constructor(private http: HttpClient) { }

  gerarPix(valor, pessoa, cart, $event) {
    return this.http.post(environment.API_URL + 'bancoBrasil/gerarPix', { valor, pessoa, cart }).pipe(btnLoading($event));
  }

  getPix(txid) {
    return this.http.post(environment.API_URL + 'bancoBrasil/getPix', { txid });
  }
}
