import { LanguageService } from './../services/language.service';
import { Pipe, PipeTransform } from '@angular/core';

/**
 * <input type="text" class="form-control" [placeholder]="'Buscar' | lang : 'HOME.buscar'">
 */
@Pipe({
  name: 'lang'
})
export class LangPipe implements PipeTransform {

  constructor(
    public languageS: LanguageService
  ) {}

  transform(value: any, ...args: any[]): any {
    if (this.languageS.lang === 'pt') { return value; }
    const mapped = this.languageS.map[args[0]];
    if (!mapped) {
      console.warn(`Mensagem não traduzida %c [${args[0]}] => "${value}"`, 'font-weight: bold; background-color: #ecefa9');
      return value;
    }
    return this.languageS.map[args[0]][this.languageS.lang];
  }

}
