import { AfterViewInit, Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { mergeMap } from 'rxjs/operators';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { StoreService } from 'src/app/services/store.service';

@Component({
  selector: 'app-lista',
  templateUrl: './lista.component.html',
  styleUrls: ['./lista.component.scss']
})
export class ListaComponent implements OnInit, AfterViewInit {
  public totalItens = 0;
  public itensPorPag = 32;
  public ordenacao = 'Selecione';
  public nPaginas = 1;
  public activePag = 0;
  public slicedItems = [];
  public loading = true;
  public estoque = [];
  public indisponivel = [];
  private exactMatch = false;

  constructor(private activRoute: ActivatedRoute, private storeS: StoreService, private gTagS: GoogleAnalyticsService) { }

  ngOnInit() {
    this.activRoute.data.subscribe((res: any) => {
      if (res.exactMatch) {
        this.exactMatch = true;
      }
    });
    this.activRoute.params.subscribe(res => {
      this.loading = true;
      this.slicedItems = [];
      this.totalItens = 0;
      switch (res.tipo) {
        case 'categoria':
          const paramId = this.activRoute.snapshot.queryParams.id;
          if (!paramId) {
            return;
          }
          this.storeS.getItensByCateg(paramId).subscribe((resB: any) => {
            scrollTo(0, 300);
            this.slicedItems = [];
            for (let i = 0; i <= 200; i++) {
              if (resB[i]) {
                this.slicedItems.push(resB[i]);
              }
            }
            this.totalItens = Object.keys(resB).length;
            [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
            this.slicedItems = [...this.estoque, ...this.indisponivel];
            // this.gTagS.viewItemList(this.slicedItems, res.termo.replace(/-/g, ' '));
            this.loading = false;
          });
          break;
        case 'search':
          this.storeS.getItensBySearchTerm(res.termo.replace(/-/g, ' '), this.exactMatch).subscribe((resC: any) => {
            scrollTo(0, 300);
            if (resC.length > 0) {
              this.slicedItems = resC;
              this.totalItens = resC.length;
              [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
              this.slicedItems = [...this.estoque, ...this.indisponivel];
              this.gTagS.viewItemList(this.slicedItems, res.termo.replace(/-/g, ' '));
            }
            this.loading = false;
          });
          break;
        default:
          this.loading = false;
          break;
      }
    });

    setTimeout(() => {
      this.updateSlicedItems();
      this.updateNumeroPaginas();
    });
  }

  ngAfterViewInit() {
    this.slicedItems = [...this.estoque, ...this.indisponivel];
  }

  selectedItensPorPag() {
    this.updateSlicedItems();
    this.updateNumeroPaginas();
  }

  changePage(index) {
    this.activePag = index;
    this.updateSlicedItems();
  }

  updateSlicedItems() {
    // this.slicedItems = this.slicedItems.slice(this.activePag * this.itensPorPag, this.activePag * this.itensPorPag + this.itensPorPag);
    this.slicedItems = [...this.estoque, ...this.indisponivel];
  }

  updateNumeroPaginas() {
    this.nPaginas = Math.ceil(this.slicedItems.length / this.itensPorPag);
    if (this.activePag >= this.nPaginas) {
      this.activePag = 0;
      this.updateSlicedItems();
    }
  }

  partition(array, filter) {
    const pass = [];
    const fail = [];
    array.forEach((e, idx, arr) => (filter(e, idx, arr) ? pass : fail).push(e));
    return [pass, fail];
  }


  ordenarLista(type: string) {
    switch (type) {
      case 'menorPreco':
        this.estoque.sort((a, b) => {
          return a.tabela?.vVenda < b.tabela?.vVenda ? -1 : a.tabela?.vVenda > b.tabela?.vVenda ? 1 : 0;
        });
        break;
      case 'maiorPreco':
        this.estoque.sort((a, b) => {
          return a.tabela?.vVenda > b.tabela?.vVenda ? -1 : a.tabela?.vVenda < b.tabela?.vVenda ? 1 : 0;
        });
        break;
      case 'alfabetica':
        this.estoque.sort((a, b) => {
          return a.item_linkWeb.linkWeb.titulo > b.item_linkWeb.linkWeb.titulo ? 1 : a.item_linkWeb.linkWeb.titulo < b.item_linkWeb.linkWeb.titulo ? -1 : 0;
        });
        break;
      default:
        break;
    }
    this.slicedItems = [...this.estoque, ...this.indisponivel];
  }
}
