import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { UserService } from 'src/app/services/user.service';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';

@Component({
  selector: 'app-pedidos',
  templateUrl: './pedidos.component.html',
  styleUrls: ['./pedidos.component.scss']
})
export class PedidosComponent implements OnInit {
  public pedidos = [];
  public loading = true;

  constructor(private readonly pedidoS: PedidoService,
              public userS: UserService,
              private modalS: ModalService,
              private router: Router) { }

  ngOnInit(): void {
    if (this.userS.user) {
      this.pedidoS.getClientPedidos().subscribe((res: any) => {
        this.pedidos = res;
        this.loading = false;
      });
    } else {
      this.modalS.createModal(MdAlertComponent, {title: 'Cliente não encontrado.', message: 'Cliente não encontrado, favor fazer o login novamente.'});
    }
  }

  detalhesPedido(pedidoWebId) {
    this.router.navigate(['/cliente/status-pedido', pedidoWebId]);
  }

}
