import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { CartService } from '../cart.service';

@Injectable({
  providedIn: 'root'
})
export class MundipaggService {

  constructor(private http: HttpClient, private cartS: CartService) { }

  gerarPagamentoCC(dadosPagamento, token, salvarCartao, cpfTitular, ) {
    return this.http.post(environment.API_URL + 'mundipagg/payment_cc', { cart: this.cartS.cart, dadosPagamento, token, salvarCartao, cpfTitular, });
  }

  checkToken() {
    return this.http.post(environment.API_URL + 'pessoa/getCards', { });
  }

  deleteToken(cardToken: any) {
    return this.http.post(environment.API_URL + 'pessoa/delCard', cardToken);
  }
}
