import { Injectable } from '@angular/core';

declare global {
  interface Window { _trustvox: any; }
}

@Injectable({
  providedIn: 'root'
})
export class TrustvoxService {
  item: any;

  constructor() { }

  configTrustvox(item: any) {
    this.item = item;
    this.setTrustvoxAttrs();
    this.addTrustvoxScript();
  }

  setTrustvoxAttrs() {
    window._trustvox = [];

    window._trustvox.push(
      ['_storeId', 4860],
      ['_productId', 1 || null],
      ['_customStyle', {}],
      ['_productName', this.item.descricao || null],
      ['_productPhotos', this.item.fotos || null]
    );
  }

  addTrustvoxScript() {
    if (!this.isTrustvoxScriptLoaded()) {
      const script = window.document.createElement('script');
      script.src =
        '//static.trustvox.com.br/sincero-staging/sincero.js';
      script.async = false;
      script.defer = true;
      document.head.appendChild(script);
    }
  }

  isTrustvoxScriptLoaded() {
    return !!document.querySelector(
      '[src=\'//static.trustvox.com.br/sincero-staging/sincero.js\']'
    );
  }
}
