import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalService } from 'src/app/services/modal.service';
import { UserService } from 'src/app/services/user.service';
import { mustMatch } from '../../../core/Functions';
import { MdAlertComponent } from '../md-alert/md-alert.component';

@Component({
  selector: 'app-md-alterarsenha',
  templateUrl: './md-alterarsenha.component.html',
  styleUrls: ['./md-alterarsenha.component.scss']
})
export class MdAlterarsenhaComponent implements OnInit {
  public form: FormGroup;
  public data: any;
  public modal: any;

  constructor(private fb: FormBuilder, private userS: UserService, private modalS: ModalService) { }

  ngOnInit() {
    this.setupForm();
  }

  setupForm() {
    this.form = this.fb.group({
      senhaAtual: [null, Validators.required],
      novaSenha: [null, [Validators.minLength(4), Validators.required]],
      novaSenha2: [null, [Validators.minLength(4), Validators.required]],
      pessoa_id: [this.userS.user.id]
    },
    {
      validators: [mustMatch('novaSenha', 'novaSenha2')]
    });
  }

  get formSenha() {
    return this.form.controls;
  }

  alterarSenha(event) {
    if (this.form.value.senhaAtual === this.form.value.novaSenha) {
      this.modalS.createModal(MdAlertComponent, {title: 'Nova Senha', message: 'Nova senha não pode ser igual a atual!'});
      return;
    }
    this.userS.alterarSenha(event, this.form.value).subscribe((res: any) => {
      this.modal.close();
      this.modalS.createModal(MdAlertComponent, {title: 'Nova Senha', message: 'Senha alterada com sucesso!'});
    });
  }
}
