import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { UserService } from 'src/app/services/user.service';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-status-pedido',
  templateUrl: './status-pedido.component.html',
  styleUrls: ['./status-pedido.component.scss']
})
export class StatusPedidoComponent implements OnInit {
  public pedido: any = {};
  public loading = true;
  public layout = environment.layout;

  constructor(private activeRoute: ActivatedRoute,
              private router: Router,
              private pedidoS: PedidoService,
              private userS: UserService,
              private modalS: ModalService) { }

  ngOnInit(): void {
    this.activeRoute.params.subscribe(params => {
      if (!params.id) {
        this.router.navigate(['/']);
      }

      this.pedidoS.getById(params.id, this.userS.user.id).subscribe((res: any) => {
        if (res.error) {
          this.modalS.createModal(MdAlertComponent, { title: 'Pedido não encontrado.', message: res.error + ' Favor entrar em contato com a nossa equipe.' });
          this.router.navigate(['/']);
          return;
        }
        this.loading = false;
        this.pedido = res[0];
      });
    });
  }

}
