import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MdRegiaoComponent } from './../../../pages/modais/md-regiao/md-regiao.component';
import { MdAlertComponent } from '../../../pages/modais/md-alert/md-alert.component';
import { MdConfirmComponent } from '../../../pages/modais/md-confirm/md-confirm.component';
import { LanguageService } from './../../../services/language.service';
import { ModalService } from './../../../services/modal.service';
import { StoreService } from './../../../services/store.service';
import { take } from 'rxjs/operators';
import { CartService } from 'src/app/services/cart.service';
import { IItem } from 'src/app/services/item.interface';
import { UserService } from 'src/app/services/user.service';
import { Router } from '@angular/router';
import { slugify } from 'src/app/core/Functions';
import { MdEsquecisenhaComponent } from 'src/app/pages/modais/md-esquecisenha/md-esquecisenha.component';
import { Location } from '@angular/common';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  subTotalCart = 0;
  public frmBusca: FormGroup;
  public frmLogin: FormGroup;
  public filialAtual = '';
  public itensSearch = [];
  private itensDB = [];
  private lastSearch = { ts: 0, txt: '', key: '' };
  public hotsite = false;
  public hotsiteBtn = false;
  public layout = environment.layout;
  public navMenuItens = [
    { nome: 'Minha Conta', routerLink: '/cliente/minha-conta', icon: 'fas fa-user' },
    { nome: 'Cadastro', routerLink: '/cliente/cadastro', icon: 'fas fa-table' },
    { nome: 'Pedidos', routerLink: '/cliente/pedidos', icon: 'fas fa-box-open' },
    { nome: 'Lista de Desejos', routerLink: '/cliente/listas-desejos', icon: 'fas fa-list' },
    { nome: 'Endereços', routerLink: '/cliente/enderecos', icon: 'fas fa-map-marked-alt' },
    { nome: 'Cursos', routerLink: '/cliente/cursos', icon: 'fas fa-book' }
  ];

  constructor(
    private fb: FormBuilder,
    public lng: LanguageService,
    public store: StoreService,
    private modalS: ModalService,
    public cartS: CartService,
    public userS: UserService,
    private router: Router,
    private location: Location,
    private gTagS: GoogleAnalyticsService
  ) { }


  ngOnInit() {
    this.setupForms();
    // tslint:disable-next-line:triple-equals
    if (this.router.url === '/sao-paulo' || localStorage.getItem('empId') == '5') {
      localStorage.setItem('empId', '5');
      this.store.getLayout(5, false);
    }
    // tslint:disable-next-line:triple-equals
    if (this.router.url === '/etna-berrini' || localStorage.getItem('empId') == '4') {
      localStorage.setItem('empId', '4');
      this.store.getLayout(4, false);
    }
    if (this.router.url === '/gardencenter') {
      localStorage.setItem('hotsite', 'true');
    } else if (this.router.url === '/') {
      localStorage.setItem('hotsite', 'false');
    }
    setTimeout(() => {
      if (this.store.layout.emp?.empWeb?.hotsite) {
        if (Object.keys(this.store.layout.emp?.empWeb?.hotsite).length <= 0) {
          localStorage.setItem('hotsite', 'false');
        }
        this.hotsiteBtn = Object.keys(this.store.layout.emp?.empWeb?.hotsite).length > 0;
      }
      this.hotsite = (localStorage.getItem('hotsite') === 'true') || false;
    }, 2000);
    if (this.layout === 'esalflores') {
      switch (localStorage.getItem('empId')) {
        case '1':
          this.filialAtual = 'Curitiba - PR (Matriz)';
          break;
        case '5':
          this.filialAtual = 'Congonhas, São Paulo - SP';
          break;
        case '4':
          this.filialAtual = 'Berrini, São Paulo - SP';
          break;
        case '6':
          this.filialAtual = 'Guarulhos, São Paulo - SP';
          break;
        default:
          this.filialAtual = 'Curitiba - PR (Matriz)';
          break;
      }
    }
    if (this.layout === 'esalpet') {
      switch (localStorage.getItem('empId')) {
        case '1':
          this.filialAtual = 'Matriz, Curitiba - PR';
          break;
        case '2':
          this.filialAtual = 'Uberaba, Curitiba - PR';
          break;
        case '3':
          this.filialAtual = 'Vila Izabel, Curitiba - PR';
          break;
        case '4':
          this.filialAtual = 'Barigui, Curitiba - PR';
          break;
        case '5':
          this.filialAtual = 'Centro Cívico, Curitiba - PR';
          break;
        case '8':
          this.filialAtual = 'Xaxim, Curitiba - PR';
          break;
        default:
          this.filialAtual = 'Matriz, Curitiba - PR';
          break;
      }
    }
  }

  excluirItemCart(index) {
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar', message: 'Deseja remover este item do carrinho?' }).then(res => {
      if (res) {
        this.gTagS.removeFromCart(this.cartS.cart.item[index]);
        this.cartS.delItem(this.cartS.cart.item[index]);
      }
    });
  }

  setupForms() {
    this.frmBusca = this.fb.group({
      busca: [],
      localizacao: []
    });
    this.store.$layoutReady
      .pipe(take(1))
      .subscribe(v => {});

    this.frmLogin = this.fb.group({
      email: [],
      password: []
    });
  }

  mudarRegiao($event) {
    $event.preventDefault();
    this.modalS.createModal(MdRegiaoComponent).then((res: any) => {
      this.filialAtual = res.cidade;
      localStorage.setItem('empId', res.id);
      this.store.getLayout(res.id, false);
      this.cartS.resetCart();
      setTimeout(() => {
        if (this.router.url === '/sao-paulo') {
          this.location.replaceState('/');
        }
        window.location.reload();
      }, 500);
    });
  }

  login($event) {
    if (!this.frmLogin.value.email || !this.frmLogin.value.password) {
      return;
    }

    this.userS.login($event, this.frmLogin.value).subscribe((res: any) => {
      this.userS.user = res.user;
      this.userS.jwt = res.jwt;
      this.frmLogin.patchValue({
        email: null,
        password: null
      });
      this.router.navigate(['/']);
    });
  }

  logoff() {
    this.modalS.createModal(MdConfirmComponent, { title: 'Sair da Conta', message: 'Deseja sair da conta?'}).then(res => {
      if (res) {
        this.userS.logoff();
      }
    });
  }

  updateQuantidade(value: string, item: IItem) {
    if (Number(value) < 1) {
      value = '1';
    }
    if (Number(value) > item.estoque.estAtual) {
      item.quantidade = item.estoque.estAtual;
      this.modalS.createModal(MdAlertComponent, {title: 'Estoque insuficiente', message: `Só há ${item.estoque.estAtual} unidade(s) deste item em estoque`});
    }
    const index = this.cartS.cart.item.indexOf(item);
    this.cartS.cart.item[index].quantidade = Number(value);
    this.cartS.updateItem(item);
  }

  alertWhats() {
    let numeroWhats = '+55 41 3091-0403';
    if (this.layout === 'esalpet') {
      numeroWhats = '+55 41 3091-0400';
    }
    this.modalS.createModal(MdAlertComponent, { title: 'Whatsapp', message: numeroWhats });
  }

  abrirLista(categ) {
    const slug = slugify(categ.categoria);
    this.router.navigate(['/lista', 'categoria', slug], { queryParams: { id: categ.id } });
  }

  search(searchTerm: string, exactMatch = false) {
    this.itensSearch = [];
    this.frmBusca.patchValue({busca: ''});
    const route = this.router.config.find(r => r.path === '').children.find(r => r.path === 'lista/:tipo/:termo');
    route.data = { exactMatch };
    const slug = slugify(searchTerm);
    this.gTagS.search(searchTerm);
    this.router.navigate(['lista', 'search', slug]);
  }

  esqueciSenha() {
    this.modalS.createModal(MdEsquecisenhaComponent);
  }

  searchSuggestion($event) {
    this.itensSearch = [];
    if ($event.timeStamp - this.lastSearch.ts > 300) {
      if (this.frmBusca.value.busca.length > 0) {
        this.lastSearch.txt = this.frmBusca.value.busca;
        if ($event.key !== 'Backspace' && this.itensDB.length > 0) {
          for (const item of this.itensDB) {
            if (item.item_linkWeb.linkWeb.titulo.toLowerCase().startsWith(this.frmBusca.value.busca.toLowerCase())) {
              this.itensSearch.push(item);
              if (this.itensSearch.length >= 5) {
                return;
              }
            }
          }
        } else {
          this.lastSearch.ts = $event.timeStamp;
          this.store.getItensBySearchTerm(this.frmBusca.value.busca).subscribe((res: any) => {
            this.itensDB = res;
            for (const item of res) {
              if (item.item_linkWeb.linkWeb.titulo.toLowerCase().startsWith(this.frmBusca.value.busca.toLowerCase())) {
                this.itensSearch.push(item);
                if (this.itensSearch.length >= 5) {
                  return;
                }
              }
            }
          });
        }
      }
    }
  }
}
