import { isPlatformBrowser, isPlatformServer } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Inject, Injectable, PLATFORM_ID, EventEmitter } from '@angular/core';
import { makeStateKey, TransferState } from '@angular/platform-browser';
import { addMinutes } from 'date-fns';
import { environment } from './../../environments/environment';
import { CacheService } from './cache.service';

// https://medium.com/@evertonrobertoauler/angular-5-universal-with-transfer-state-using-angular-cli-19fe1e1d352c
const LAYOUT_KEY = makeStateKey('layout');

const layoutInit: ILayout = {
  emp: {}
};

@Injectable({
  providedIn: 'root'
})
export class StoreService {

  public readonly API_URL = environment.API_URL;
  public readonly IMG_URL = environment.IMG_URL;

  public layout: ILayout = {...layoutInit};
  public $layoutReady: EventEmitter<any> = new EventEmitter();

  constructor(@Inject(PLATFORM_ID) private platformId,
              private http: HttpClient,
              private state: TransferState,
              private cacheS: CacheService) {
    this.getLayout(Number(localStorage.getItem('empId')) || 1);
  }

  getLayout(empId: number, cache = true) {
    /**
     * 1. No browser, tenta pelo localStorage
     * 2. Caso contrário pega pelo transferState (do servidor)
     * 3. Caso não tenha transferState, faz requisição local
     */
    if (isPlatformBrowser(this.platformId)) {
      // localStorage
      if (cache) {
        const json = JSON.parse(localStorage.getItem('layout'));
        if (json) {

          // Processar exp do objeto local, para casos de layout antigo
          if ( json._exp && Date.now() > Number(json._exp) ) {
            return this.getLayoutBuilder(json.emp.id);
          }
          return this.setLayout(json);
        }
        // Transfer state
        const cachedServer = this.state.get(LAYOUT_KEY, null);
        if (cachedServer) {
          return this.setLayout(cachedServer);
        }
      }

      this.getLayoutBuilder(empId);
    }

    /**
     * 1. Sempre faz a requisição na matriz
     */
    if (isPlatformServer(this.platformId)) {
      // Faz requisição
      this.getLayoutBuilder(empId);
    }

  }

  setLayout(layout) {
    this.layout = layout;

    // Layout Ready - setTimeout pois alguns components podem dar listener nesse evento depois de disparado
    setTimeout(() => this.$layoutReady.emit());

    return this.layout;
  }

  getLayoutBuilder(emp_id) {
    this.http.get<any>(environment.API_URL + 'layoutBuilder/getEmp/' + emp_id, {})
      .subscribe(layout => {
        // Seta layout local
        this.setLayout(layout);

        // State server
        this.state.set(LAYOUT_KEY, layout);

        // Caso esteja no browser, sempre salva no localStorage
        if (isPlatformBrowser(this.platformId)) {
          layout._exp = addMinutes(new Date(), 30).getTime();
          localStorage.setItem('layout', JSON.stringify(layout));
        }
    });
  }

  getItemById(id) {
    return this.http.get(`${environment.API_URL}item/getById/${id}`);
    // return this.cacheS.getOrSetCache(`StoreService_getItemById_${id}_${this.layout.emp.id}`, this.http.get(`${environment.API_URL}item/getById/${id}`), 300000);
  }

  getItensByCateg(id) {
    // return this.http.get(`${environment.API_URL}getItensByCategId/${id}`);
    return this.cacheS.getOrSetCache(`StoreService_getItensByCateg_${id}_${this.layout.emp.id}`, this.http.get(`${environment.API_URL}getItensByCategId/${id}`), 300000);
  }

  getItensBySearchTerm(term, exactMatch = false) {
    return this.http.post(environment.API_URL + 'getItensBySearchTerm', { term, exactMatch });
  }

}
