import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-md-confirm',
  templateUrl: './md-confirm.component.html',
  styleUrls: ['./md-confirm.component.scss']
})
export class MdConfirmComponent implements OnInit {
  public modal: any;
  public data: any;
  public resolve: any;

  constructor() { }

  ngOnInit(): void {
  }

  cancelar() {
    this.resolve = false;
    this.modal.close();
  }

  confirmar() {
    this.resolve = true;
    this.modal.close();
  }

}
