import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-md-esquecisenha',
  templateUrl: './md-esquecisenha.component.html',
  styleUrls: ['./md-esquecisenha.component.scss']
})
export class MdEsquecisenhaComponent implements OnInit {
  public modal: any;
  public email: string;
  public validaEmail = /^\S+@\S+\.\S+$/;
  public enviado = false;

  constructor(private userS: UserService) { }

  ngOnInit(): void {
  }

  esqueciSenha($event) {
    this.userS.esqueciSenha(this.email, $event).subscribe((res: any) => {
      this.enviado = true;
    });
  }
}
