import { UserService } from './../services/user.service';
import { Injectable } from '@angular/core';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { finalize, catchError, map } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { ModalService } from '../services/modal.service';
import { MdAlertComponent } from '../pages/modais/md-alert/md-alert.component';
import { environment } from 'src/environments/environment';

@Injectable()
export class ErpInterceptor implements HttpInterceptor {
  public static base = 'erp_' + environment.layout;
  public static useErpHeader = true;

  constructor(private userS: UserService, private modalS: ModalService) {}

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const started = Date.now();
    const empId = localStorage.getItem('empId');

    // Headers
    const userJwt = this.userS.user ? this.userS.jwt : '';

    let authReq = req.clone();
    if (!ErpInterceptor.useErpHeader) {
      ErpInterceptor.useErpHeader = true;
      return next.handle(authReq).pipe(finalize(() => ErpInterceptor.useErpHeader = true));
    }

    authReq = req.clone({
      headers: new HttpHeaders({
        Accept: 'application/json',
        Authorization: userJwt,
        BaseConn: JSON.stringify({base: ErpInterceptor.base, emp_id: empId || 1})
      })
    });

    // https://blog.angularindepth.com/top-10-ways-to-use-interceptors-in-angular-db450f8a62d6
    return next.handle(authReq)
    .pipe(
      map((event: HttpEvent<any>) => {
        // Ignora outros eventos - OPTIONS por exemplo
        if (!(event instanceof HttpResponse)) { return event; }
        // HTTP Status
        if (event.status === 200) {
          // Processa status do JSON
          if (event.body.status === false) {
            throw new Error(event.body.data?.error || event.body.data);
          }
          // Se tem data padrão, retorna
          if (event.body.data) {
            return event.clone({body: event.body.data});
          }
        }
      }, error => {
         // Operation failed; error is an HttpErrorResponse
        console.error('HTTP failed', error);
      }),
      catchError((error: HttpErrorResponse) => {
        // 401 handled in auth.interceptor
        if (error.status === 401) {}

        if (error.status === 0) {
          this.modalS.createModal(MdAlertComponent, {title: 'Falha de conexão.', message: 'Verifique sua internet e tente novamente. Se o problema persistir, entre em contato com a nossa equipe.'});
        }

        if (error.message.startsWith('Usuário ou senha')) {
          this.modalS.createModal(MdAlertComponent, {title: 'Usuário ou senha inválidos.', message: 'Verifique os dados digitados e tente novamente.'});
        }

        if (error.message.startsWith('Esse email já está cadastrado')) {
          this.modalS.createModal(MdAlertComponent, {title: 'Já existe esse email cadastrado na base de dados.', message: 'Esse email já está cadastrado na base de dados. Em caso de dúvidas, entre em contato com nossa equipe'});
        }

        if (error.message.startsWith('Esse cpf já está cadastrado')) {
          this.modalS.createModal(MdAlertComponent, {title: 'Já existe esse CPF cadastrado na base de dados.', message: 'Esse CPF já está cadastrado na base de dados. Em caso de dúvidas, entre em contato com nossa equipe'});
        }

        if (error.message.startsWith('Esse cnpj já está cadastrado')) {
          this.modalS.createModal(MdAlertComponent, {title: 'Já existe esse CNPJ cadastrado na base de dados.', message: 'Esse CNPJ já está cadastrado na base de dados. Em caso de dúvidas, entre em contato com nossa equipe'});
        }

        if (error.message.startsWith('Cpf/Cnpj não informado')) {
          this.modalS.createModal(MdAlertComponent, {title: 'Falha ao cadastrar.', message: 'CPF/CNPJ não informado.'});
        }

        if (error.message.includes(`for key 'unique_cpf'`)) {
          this.modalS.createModal(MdAlertComponent, {title: 'Já existe esse CPF cadastrado na base de dados.', message: 'Esse CPF já está cadastrado na base de dados. Em caso de dúvidas, entre em contato com nossa equipe'});
          return throwError('CPF já cadastrado');
        }

        if (error.message.startsWith('Senha atual incorreta')) {
          this.modalS.createModal(MdAlertComponent, {title: 'Senha atual incorreta.', message: 'Verifique os dados digitados e tente novamente.'});
        }

        if (error.error === 'Expired token') {
          this.userS.logoff();
        }

        return throwError(error);
      }),
      finalize(() => {
        const elapsed = Date.now() - started;
        const msg = `${req.method} "${req.urlWithParams}" ${elapsed} ms.`;
        console.warn(msg);
      })
    );
  }

}
