import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';
import { ModalService } from 'src/app/services/modal.service';
import { UserService } from 'src/app/services/user.service';
import { MdAlertComponent } from '../md-alert/md-alert.component';

@Component({
  selector: 'app-md-updateendereco',
  templateUrl: './md-updateendereco.component.html',
  styleUrls: ['./md-updateendereco.component.scss']
})
export class MdUpdateenderecoComponent implements OnInit {
  public modal: any;
  public data: any;
  public form: FormGroup;
  private formEnvio = {};

  constructor(private fb: FormBuilder, private http: HttpClient, private userS: UserService, private modalS: ModalService) { }

  ngOnInit(): void {
    this.setupForm();
  }

  setupForm() {
    const u = this.userS.user;
    const indexOne = this.data.index === 1 ? true : false;
    this.form = this.fb.group({
      id: [u.id],
      cep: [indexOne ? u.cep : u.cobrCep || null, Validators.required],
      logradouro: [indexOne ? u.logradouro : u.cobrLogradouro || null, Validators.required],
      numero: [indexOne ? u.numero : u.cobrNumero || null, Validators.required],
      complemento: [indexOne ? u.complemento : u.cobrComplemento || null],
      bairro: [indexOne ? u.bairro : u.cobrBairro || null, Validators.required],
      municipio: [indexOne ? u.municipio : u.cobrMunicipio || null, Validators.required],
      uf: [indexOne ? u.uf : u.uf || null, [Validators.required, Validators.minLength(2), Validators.maxLength(2)]]
    });
  }

  buscarCep(cep) {
    if (!cep) {
      return;
    }
    ErpInterceptor.useErpHeader = false;
    const validacep = /^[0-9]{8}$/;
    cep = cep.replace(/\D/g, '');
    if (validacep.test(cep)) {
      const urlViacep = `https://viacep.com.br/ws/${cep}/json/`;
      this.http.get(urlViacep).subscribe((res: any) => {
        if (res.erro) {
          this.form.patchValue({cep: ''});
          this.modalS.createModal(MdAlertComponent, {title: 'CEP não encontrado.', message: 'Verifique o CEP digitado e tente novamente.'});
          return;
        } else {
          this.form.patchValue({
            cep,
            logradouro: res.logradouro,
            bairro: res.bairro,
            municipio: res.localidade,
            uf: res.uf,
            complemento: res.complemento,
          });
          document.getElementById('numero').focus();
        }
      });
    } else {
      this.form.patchValue({cep: ''});
      this.modalS.createModal(MdAlertComponent, {title: 'CEP inválido!', message: 'Digite um CEP válido.'});
    }
  }

  setupEnvio() {
    if (!this.data.index) {
      this.modalS.createModal(MdAlertComponent, {title: 'ERRO', message: 'Um erro ocorreu, favor fechar e abrir o modal novamente.'});
      return;
    }
    if (this.data.index === 1) {
      this.formEnvio = this.form.value;
      return;
    }
    if (this.data.index === 2) {
      this.formEnvio = {
        id: this.userS.user.id,
        cobrCep: this.form.value.cep,
        cobrLogradouro: this.form.value.logradouro,
        cobrNumero: this.form.value.numero,
        cobrBairro: this.form.value.bairro,
        cobrUf: this.form.value.uf,
        cobrComplemento: this.form.value.complemento,
        cobrMunicipio: this.form.value.municipio
      };
      return;
    }
  }



  updateEndereco($event) {
    this.setupEnvio();
    this.userS.updateEndereco(this.formEnvio, $event).subscribe((res: any) => {
      this.userS.user = res;
      this.modal.close();
      this.modalS.createModal(MdAlertComponent, {title: 'Endereço', message: 'Endereço salvo com sucesso!'});
    });
  }
}
