import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { MdAlertComponent } from '../modais/md-alert/md-alert.component';
import { TrabalheService } from 'src/app/services/trabalhe.service';

@Component({
  selector: 'app-trabalhe-conosco',
  templateUrl: './trabalhe-conosco.component.html',
  styleUrls: ['./trabalhe-conosco.component.scss']
})
export class TrabalheConoscoComponent implements OnInit {
  public formTrabalheConosco: FormGroup;
  isFileChosen = false;
  public showErrors = false;
  fileName = '';
  constructor(public fb: FormBuilder, private router: Router, private modalS: ModalService, private trabalheS: TrabalheService) { }

  ngOnInit(): void {
    this.setupForm();
  }

  get form() {
    return this.formTrabalheConosco.controls;
  }

  setupForm() {
    this.formTrabalheConosco = this.fb.group({
      nome: [null, [Validators.required]],
      email: [null, [Validators.email, Validators.required]],
      fone: [null, [Validators.minLength(8), Validators.required]],
      areaDeInteresse: [null, [Validators.required]],
      curriculo: [null, [Validators.required]]
    });
  }

  handleFileInput(file){
    if (file && file.length > 0) {
      const curriculo = (file as File);
      this.isFileChosen = true;
      this.fileName = curriculo[0].name;
      this.formTrabalheConosco.get('curriculo').setValue(curriculo[0]);
    }
  }

  submitForm($event) {
  //  if (this.formTrabalheConosco.invalid) {
  //    this.showErrors = true;
 //     return;
//    }
//    else {
      document.getElementById('btnSubmit').classList.add('disable');
      this.getBase64(this.formTrabalheConosco.value.curriculo)
      .then(res => {
        this.trabalheS.postFile({doc: res}, $event).subscribe((resp: any) => {
        });
        this.router.navigate(['/']);
        this.modalS.createModal(MdAlertComponent, {title: 'Formulário preenchido com sucesso', message: 'Agora é só aguardar nosso contato!'});
      })
      .catch(error => {
        this.modalS.createModal(MdAlertComponent, {title: 'Não conseguimos ler o arquivo', message: 'Tente novamente ou escolha outro formato de arquivo!'});
        document.getElementById('btnSubmit').classList.remove('disable');
      });
//     }
  }

  getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => { resolve(reader.result); };
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  }
}
