import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { btnLoading } from '../../core/Functions';

@Injectable({
  providedIn: 'root'
})
export class PicpayService {

  constructor(private http: HttpClient) { }

  gerarQrCode(payload, $event) {
    // pessoa, value e referenceId necessários
    return this.http.post(`${environment.API_URL}picpay/payments`, payload).pipe(btnLoading($event));
  }

  status(referenceId) {
    return this.http.post(`${environment.API_URL}picpay/status`, { referenceId });
  }
}
