import { Component, OnInit, ViewChild, ViewContainerRef, AfterViewInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { ModalService } from './services/modal.service';
import { PedidoService } from './services/pedido.service';
import { UserService } from './services/user.service';
import { environment } from 'src/environments/environment';
import { Title } from '@angular/platform-browser';
import { filter, map, switchMap } from 'rxjs/operators';

declare global {
  // tslint:disable-next-line:interface-name
  interface Window { dataLayer: any; }
}
declare let gtag: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements AfterViewInit, OnInit {
  cConsent = false;
  delay = false;
  public layout = environment.layout;

  @ViewChild('bootstrapModal', {read: ViewContainerRef}) bootstrapModal: ViewContainerRef;

  constructor(private modal: ModalService,
              private userS: UserService,
              public router: Router,
              private pedidoS: PedidoService,
              activatedRoute: ActivatedRoute,
              private titleS: Title) {
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
          gtag('config', 'G-DVNXL9MM7X',
                {
                  page_path: event.urlAfterRedirects
                }
               );
       }
       router.events
       .pipe(filter((event) => event instanceof NavigationEnd))
       .pipe(map(() => activatedRoute))
       .pipe(
        map((route) => {
          while (route.firstChild) route = route.firstChild;
          return route;
        })
      )
      .pipe(switchMap((route) => route.data))

      .subscribe((event) => titleS.setTitle(event['titulo']));



    });
    // recaptcha
    const recaptchaScript = document.createElement('script');
    recaptchaScript.setAttribute('src', `https://www.google.com/recaptcha/api.js?render=${environment.recaptchaToken}`);
    document.body.append(recaptchaScript);
    // jivo
    const jivoScript = document.createElement('script');
    jivoScript.setAttribute('src', environment.jivoSrc);
    jivoScript.setAttribute('async', 'true');
    document.body.append(jivoScript);

    if (environment.layout === 'esalpet') {
      this.titleS.setTitle('Esalpet | PetShop');
      document.querySelector<HTMLLinkElement>('#appFavicon').href = '../assets/images/favicon-pet.ico';
    }
  }

  ngOnInit() {
    if (this.getLocalStorageSize() > 2) {
      localStorage.clear();
    }
    if (localStorage.getItem('user') === 'undefined' || localStorage.getItem('user') === 'null') {
      localStorage.removeItem('user');
      setTimeout(() => {
        window.location.reload();
      }, 300);
    }
    if (this.userS.user) {
      this.userS.checkJwt();
    }
    setTimeout(() => {
      this.cConsent = Boolean(localStorage.getItem('cConsent'));
      this.delay = true;
    }, 1500);
  }

  getLocalStorageSize = () => {
    let total = 0;
    // tslint:disable-next-line:forin
    for (const x in localStorage) {
        // Value is multiplied by 2 due to data being stored in `utf-16` format, which requires twice the space.
        const amount = (localStorage[x].length * 2) / 1024 / 1024;
        if (!isNaN(amount) && localStorage.hasOwnProperty(x)) {
            total += amount;
        }
    }
    return Number(total.toFixed(2));
  }

  ngAfterViewInit() {
    this.modal.bootstrapModal = this.bootstrapModal;
    this.pedidoS.checkAbandonedCart();
  }

  acceptCookie() {
    localStorage.setItem('cConsent', 'true');
    this.cConsent = true;
  }
}
