import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import { btnLoading } from '../core/Functions';
import { IUser } from './user.interface';
import { isPlatformBrowser } from '@angular/common';
import { CartService } from './cart.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private _user: IUser = null;
  private _jwt = null;

  constructor(@Inject(PLATFORM_ID) private platformId, private http: HttpClient, private cartS: CartService, private router: Router) {
  }

  get user(): IUser | null {
    if (!this._user && isPlatformBrowser(this.platformId)) {
      this._user = JSON.parse(localStorage.getItem('user'));
    }
    return this._user;
  }

  set user(user: IUser) {
    this._user = user;
    this.cartS.cliente = user;
    localStorage.setItem('user', JSON.stringify(user));
  }

  set jwt(jwt) {
    this._jwt = jwt;
    localStorage.setItem('jwt', JSON.stringify(jwt));
  }

  get jwt() {
    if (!this._jwt) {
      this._jwt = JSON.parse(localStorage.getItem('jwt'));
    }
    return this._jwt;
  }

  logoff() {
    this.user = null;
    this.jwt = null;
    localStorage.setItem('goCheckout', 'false');
    this.router.navigate(['/']);
  }

  login($event: any, form: any, loading = true) {
    /*
      form = {email: 'rodolfojnn@hotmail.com',
       password: 'mudar@1234'}
    */
    if (loading){
      return this.http.post<IUser>(environment.API_URL + 'pessoa/login', form).pipe(btnLoading($event));
    }
    else{
      return this.http.post<IUser>(environment.API_URL + 'pessoa/login', form);
    }

  }

  registerPessoa($event: any, form: any) {
    return this.http.post(environment.API_URL + 'pessoa/registerPessoa', form);
  }

  updatePessoa($event: any, form: any) {
    return this.http.post(environment.API_URL + 'pessoa/updatePessoa', form).pipe(btnLoading($event));
  }

  alterarSenha($event: any, form: any) {
    return this.http.post(environment.API_URL + 'pessoa/alterarSenha', form).pipe(btnLoading($event));
  }

  updateEndereco(formEnvio: any, $event) {
    return this.http.post(environment.API_URL + 'pessoa/updateEndereco', formEnvio).pipe(btnLoading($event));
  }

  excluirEndereco(formEnvio: any, $event) {
    return this.http.post(environment.API_URL + 'pessoa/excluirEndereco', formEnvio);
  }

  esqueciSenha(email, $event) {
    return this.http.post(environment.API_URL + 'pessoa/esqueciSenha', { email }).pipe(btnLoading($event));
  }

  redefinirSenha(form, $event) {
    return this.http.post(environment.API_URL + 'pessoa/redefinirSenha', form).pipe(btnLoading($event));
  }

  excluirConta(id) {
    return this.http.post(environment.API_URL + 'pessoa/excluirConta', { id });
  }

  checkJwt() {
    this.http.get(environment.API_URL + 'pessoa/checkJwt').subscribe((res: any) => {
      if (res[0] !== 'OK') {
        this.logoff();
        return;
      }
    });
  }
}
