import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';
import { environment } from 'src/environments/environment';
import { MdAlertComponent } from '../modais/md-alert/md-alert.component';

@Component({
  selector: 'app-imprensa',
  templateUrl: './imprensa.component.html',
  styleUrls: ['./imprensa.component.scss']
})
export class ImprensaComponent implements OnInit {
  public formImpresa: FormGroup;
  constructor(public fb: FormBuilder, private router: Router, private modalS: ModalService) { }
  public showErrors = false;
  public layout = environment.layout;

  setupForm() {
    this.formImpresa = this.fb.group({
      nome: [null, [Validators.required]],
      email: [null, [Validators.email, Validators.required]],
      fone: [null, [Validators.minLength(8), Validators.required]],
      assunto: [null, [Validators.required]],
      mensagem: [null, [Validators.required]]
    });
  }

  get form() {
    return this.formImpresa.controls;
  }

  ngOnInit(): void {
    this.setupForm();
  }

  submitForm() {
    if (this.formImpresa.invalid) {
      this.showErrors = true;
      return;
    }
    else {
      // fluxo mandar pro back
      this.router.navigate(['/']);
      this.modalS.createModal(MdAlertComponent, {title: 'Formulário preenchido com sucesso', message: 'Agora é só aguardar nosso contato!'});
    }
  }

}
