import { AfterViewInit, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-redirect-newsletter',
  templateUrl: './redirect-newsletter.component.html',
  styleUrls: ['./redirect-newsletter.component.scss']
})
export class RedirectNewsletterComponent implements OnInit, AfterViewInit {

  constructor(private router: Router) { }

  ngOnInit(): void {

  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      if (innerWidth < 600) {
        scrollTo({
          top: 371,
          behavior: 'auto'
        });
      } else {
        scrollTo({
          top: 189,
          behavior: 'auto'
        });
      }
    });
  }

  redirect(rota: string) {
    this.router.navigateByUrl(rota);
    setTimeout(() => {
      window.location.reload();
    }, 500);
  }
}
