import { isPlatformServer } from '@angular/common';
import { Directive, ElementRef, OnInit, Inject, PLATFORM_ID, NgZone } from '@angular/core';

@Directive({
  selector: '[appOwlCarousel]'
})
export class OwlCarouselDirective implements OnInit {

  constructor(
    @Inject(PLATFORM_ID) private platformId,
    private el: ElementRef<HTMLElement>,
    private ngZone: NgZone
  ) {}

  ngOnInit() {
    if (isPlatformServer(this.platformId)) { return; }

    const el = $(this.el.nativeElement);
    const dataItems = el.data('items');
    const dataNav = el.data('nav');
    const dataDots = el.data('dots');
    const dataMargin = el.data('margin');
    this.ngZone.runOutsideAngular(() => {
      setTimeout(() => {
        (el as any).owlCarousel({
          loop: false,
          rewind: true,
          margin: dataMargin,
          nav: dataNav,
          dots: dataDots,
          autoplay: true,
          items: dataItems,
          autoplayTimeout: 10000,
          navText: ['<i class="fa fa-chevron-left"></i>', '<i class="fa fa-chevron-right"></i>'],
            // items: 4,
          responsive: {
            0: {
              items: 2,
              center: false,
              nav: false,
              autoWidth: false,
              autoplay: false
            },
            600: {
              items: 2,
              nav: true,
            },
            800: {
              items: 3,
              nav: true
            },
            1100: {
              items: dataItems,
              autoplay: true,
              autoplayHoverPause: true
            }
          }
        });
      });
    });
  }

}
