import { N2 } from './Functions';

/*
Exemplos
  const result = new Collx(arr)
    .where({'aberto': false})
    .sortBy('id', -1)
    .toArray();
*/

export class Collx {
  // tslint:disable-next-line:variable-name
  private _value: any[] = [];

  constructor(arr) {
    this._value = [].concat(arr); // Clone array
  }

  push(val) {
    this._value.push(val);
    return this;
  }

  indexOf(cb): number {
    return this._value.indexOf(this._value.find(cb));
  }

  update(where, value) {
    const filtered = this._filter(where);
    if (filtered.length) {
      this._value[this._value.indexOf(filtered[0])] = value;
    }
    return this;
  }

  /*
  this.adquirentes = new Collx(this.adquirentes)
         .updateOrCreate(it => it.id === res.id, res)
         .toArray();
  */
  updateOrCreate(where, value, prepend = false) {
    const filtered = this._filter(where);
    if (filtered.length) {
      this._value[this._value.indexOf(filtered[0])] = value;
    } else {
      if (prepend) {
        this._value.unshift(value);
      } else {
        this._value.push(value);
      }
    }
    return this;
  }

  // Deferente dos outros métodos update, esse atualiza só os campos presentes no value
  /*
    .updateFields(it => it.id === id, this.frmMain.value)
  */
  updateFields(where, value) {
    const filtered = this._filter(where);
    if (filtered.length) {
      const idx = this._value.indexOf(filtered[0]);
      this._value[idx] = {...this._value[idx], ...value};
    }
    return this;
  }

  keyBy(prop) {
    this._value = this._value.reduce((a, b) => {
      a[b[prop]] = b;
      return a;
    }, {});
    return this;
  }

  // .remove('id', 5)
  remove(prop, value) {
    this._value = this._filter(it => it[prop] !== value);
    return this;
  }

  // .sortBy('id', -1)
  sortBy(field, desc = 1) {
    this._value.sort((a, b) => {
      if (typeof a[field] === 'string' && typeof b[field] === 'string') {
        return a[field].localeCompare(b[field]) * desc;
      }
      return (Number(a[field]) - Number(b[field])) * desc;
    });
    return this;
  }

  /*
    .sortFn((a, b) => {
      const _nome = a.nome.localeCompare(b.nome);
      const _online = a._online - b._online;
      return -_online || _nome;
    })
  */
  sortFn(fn) {
    this._value.sort(fn);
    return this;
  }

  // .where({'aberto': false})
  where(anyType, operator?, value?) {
    if (typeof anyType === 'object') {
      Object.keys(anyType).forEach(key => {
        const split = key.split('.');
        switch (split.length) {
          case 1: this._value = this._filter((it: any) => it[key] === anyType[key]); break;
          case 2: this._value = this._filter((it: any) => it[split[0]][split[1]] === anyType[key]); break;
        }
      });
      return this;
    }
    if (typeof anyType === 'function') {
      this._value = this._filter(anyType);
      return this;
    }

    // anyType === string
    let callback: any = () => {};
    switch (operator) {
      case '>':
        callback = it => it[anyType] > value; break;
      case '<':
        callback = it => it[anyType] < value; break;
    }
    this._value = this._filter(callback);
    return this;
  }

  // const pet_especie = new Collx(this.petEspecieDS).find(res.petEspecieId);
  find(value) {
    return this._value.find(it => it.id !== value);
  }

  pluck(field) {
    return this._value.map(v => v[field]);
  }

  /*
  this.categorias = new Collx(data)
    .uniqueBy(this.frmMain.get('categorias').value, 'id')
    .toArray();
  */
  uniqueBy(targetArr, prop) {
    let i = this._value.length;
    const filtered = [];
    while (i--) {
      if (targetArr.some(it => it[prop] === this._value[i][prop])) {
        continue;
      }
      filtered.push(this._value[i]);
    }
    this._value = filtered;
    return this;
  }

  chunkBy(size) {
    const results = [];
    while (this._value.length > 0) {
      results.push(this._value.splice(0, size));
    }
    this._value = results;
    return this;
  }

  distinct(prop) {
    const flags = {};
    this._value = this._filter(it => {
      if (flags[it[prop]]) { return false; }
      return flags[it[prop]] = true;
    });
    return this;
  }

  except(arr: string[]) {
    arr.forEach(key => {
      delete this._value[key];
    });
    return this;
  }

  // .groupBy('usuario_id')
  groupBy(prop) {
    let i = this._value.length;
    const resA = {};
    while (i--) {
      const arr = resA[this._value[i][prop]];
      if (!resA[this._value[i][prop]]) { resA[this._value[i][prop]] = []; }
      resA[this._value[i][prop]].push(this._value[i]);
    }
    // Keys
    const resB = [];
    const keys = Object.keys(resA);
    let n = keys.length;
    while (n--) {
      resB.push({
        $key: keys[n],
        $val: resA[keys[n]]
      });
    }
    this._value = resB;
    return this;
  }

  private _filter(cb) {
    let i = this._value.length;
    const filtered = [];
    while (i--) {
      if (cb(this._value[i])) {
        filtered.push(this._value[i]);
      }
    }
    filtered.reverse();
    return filtered;
  }

  // new Collx(array).sum('valor');
  sum(prop): number {
    /*
    const val = this._value = this._value.reduce((a, b) => {
      a = a + Number(b[prop])
      return a;
    }, 0);
    */
    let res = 0;
    for (let i = 0, len = this._value.length; i < len; i++) {
      if (!this._value[i]) { continue; }
      res += +this._value[i][prop];
    }
    return N2(res);

  }

  toArray(): any[] {
    return this._value;
  }

  toValue() {
    return this._value;
  }

}
