import { HttpClient } from '@angular/common/http';
import { Injectable, PLATFORM_ID, Inject } from '@angular/core';
import { environment } from './../../environments/environment';
import { Collx } from './../core/Collx';
import { ICart } from './cart.interface';
import { IItem } from './item.interface';
import { isPlatformBrowser, isPlatformServer } from '@angular/common';

const cartInit: ICart = {
  cliente: {},
  entrega: {},
  item: [],
  valorTotalItems: 0,
  valorTotal: 0,
  cupom: null,
  desconto: 0,
  pagamento: {},
  tokenCard:{},
};

@Injectable({
  providedIn: 'root'
})
export class CartService {

  private ɵcart: ICart = null;

  constructor(@Inject(PLATFORM_ID) private platformId, private http: HttpClient) {}

  get cart(): ICart {
    // Se já está definido, retorna
    if (this.ɵcart) { return this.ɵcart; }

    if (isPlatformServer(this.platformId)) {
      return this.ɵcart = {...cartInit};
    }

    if (isPlatformBrowser(this.platformId)) {
      let localS = JSON.parse(localStorage.getItem('cart'));
      if (!localS) { localS = { ...cartInit }; }
      this.cart = localS;
    }

    this.updateTotalItems();

    return this.ɵcart;
  }

  set cart(cart: ICart) {
    this.ɵcart = cart;
    this.updateTotalItems();
    // Save localStorage
    this.saveLocal();
  }

  set entrega(entrega: any) {
    this.cart.entrega = entrega;
    localStorage.setItem('cartExpireAt', JSON.stringify(Date.now() + 86400000));
    this.saveLocal();
  }

  get entrega() {
    return JSON.parse(localStorage.getItem('cart')).entrega;
  }

  set item(item: any) {
    this.cart.item = item;
    this.saveLocal();
    this.updateTotalItems();
  }

  set desconto(desconto: number) {
    this.cart.desconto = desconto;
    this.saveLocal();
  }

  set cupom(cupom: any) {
    this.cart.cupom = cupom;
    this.saveLocal();
  }

  get cupom() {
    return JSON.parse(localStorage.getItem('cart')).cupom;
  }

  set cliente(cliente: any) {
    this.cart.cliente = cliente;
    this.saveLocal();
  }

  set valorTotal(valorTotal: any) {
    this.cart.valorTotal = valorTotal;
    this.saveLocal();
  }

  get valorTotal() {
    return JSON.parse(localStorage.getItem('cart')).valorTotal;
  }

  set pagamento(pagamento: any) {
    this.cart.pagamento = pagamento;
    this.saveLocal();
  }
  set tokenCard(tokenCard: any) {
    this.cart.tokenCard = tokenCard;
    this.saveLocal();
  }





  saveLocal() {
    localStorage.setItem('cart', JSON.stringify(this.ɵcart));
  }

  addItem(item: IItem) {
    const foundIndex = this.ɵcart.item.findIndex(el => el.id === item.id);
    if (foundIndex >= 0) {
      this.ɵcart.item[foundIndex].quantidade += item.quantidade;
    } else {
      this.ɵcart.item = [...this.ɵcart.item, item];
    }

    this.updateTotalItems();
    this.saveLocal();
  }

  delItem(item: IItem) {
    this.ɵcart.item = new Collx(this.ɵcart.item)
      .remove('id', item.id)
      .toArray();
    this.updateTotalItems();
    this.saveLocal();
  }

  resetCart() {
    this.cart = {...cartInit};
  }

  updateItem(item: IItem) {
    this.ɵcart.item[this.ɵcart.item.indexOf(item)] = item;
    this.updateTotalItems();
    this.saveLocal();
  }

  updateTotalItems() {
    this.ɵcart.valorTotalItems = 0;
    for (const item of this.ɵcart.item) {
      this.ɵcart.valorTotalItems += (item.tabela?.vPromo || item.tabela?.vVenda) * item.quantidade;
    }
    this.saveLocal();
  }

  getFreteByCep(cep) {
    return this.http.post(`${environment.API_URL}transporte/getValorFrete`, {cep});
  }

  getTransportePeriodo(empId) {
    return this.http.post(environment.API_URL + 'transporte/getPeriodo', { empId });
  }

  getCorreiosPrazoPreco(peso, cepDestino) {
    return this.http.post(environment.API_URL + 'correios/calc', { peso, cepDestino });
  }
}
