import { MdConfirmComponent } from './../../modais/md-confirm/md-confirm.component';
import { MdAlertComponent } from './../../modais/md-alert/md-alert.component';
import { ModalService } from 'src/app/services/modal.service';
import { Component, OnInit } from '@angular/core';
import { CartService } from 'src/app/services/cart.service';
import { IItem } from 'src/app/services/item.interface';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';
import { HttpClient } from '@angular/common/http';
import { MdNaoseicepComponent } from '../../modais/md-naoseicep/md-naoseicep.component';
import { StoreService } from 'src/app/services/store.service';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { environment } from 'src/environments/environment';


@Component({
  selector: 'app-carrinho',
  templateUrl: './carrinho.component.html',
  styleUrls: ['./carrinho.component.scss']
})
export class CarrinhoComponent implements OnInit {
  public valorMinimo = 1;
  public valorTotalCart = 0;
  public showFrete = false;
  public loadingCep = false;
  frete = 0;
  public cep: any;
  public layout = environment.layout;
  public vFreteGratis = null;
  public codCupom = '';
  public loadingCupom = false;
  public cupomAplicado = false;

  constructor(public cartS: CartService,
              private router: Router,
              private userS: UserService,
              private http: HttpClient,
              private modalS: ModalService,
              private storeS: StoreService,
              private gTagS: GoogleAnalyticsService,
              private activeRoute: ActivatedRoute,
              private pedidoS: PedidoService) { }

  async ngOnInit() {
    const paramId = this.activeRoute.snapshot.paramMap.get('id');
    if (paramId) {
      if (!this.userS.user) {
        this.router.navigate(['/login']);
        this.modalS.createModal(MdAlertComponent, { title: 'Recuperar carrinho abandonado.', message: 'Faça o login para recuperar o carrinho abandonado.' });
        localStorage.setItem('goAbandonedCart', paramId);
        return;
      }
      if (this.userS.user) {
          const res: any = await this.pedidoS.getById(paramId, this.userS.user.id).toPromise();
          if (res.error) {
            this.modalS.createModal(MdAlertComponent, { title: 'Carrinho não encontrado ou pertence a outro usuário.', message: 'O carrinho abandonado não foi encontrado ou pertence a outro usuário. Caso isso seja um erro, favor entrar em contato com a nossa equipe.' });
            this.router.navigate(['/']);
            return;
          }
          if (res[0].status !== 'Abandonado') {
            this.modalS.createModal(MdAlertComponent, { title: 'Carrinho com status diferente de abandonado.', message: 'O carrinho informado está com status ' + res[0].status + '. Caso isso seja um erro, favor entrar em contato com a nossa equipe.' });
            this.router.navigate(['/']);
            return;
          }
          this.cartS.entrega = res[0].json.entrega;
          this.cartS.item = res[0].json.item;
      }
    }
    if (this.cartS.cupom) {
      this.codCupom = this.cartS.cart.cupom.cupom;
      this.cupomAplicado = true;
    }
  }

  excluirItemCart(index) {
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar', message: 'Deseja remover este item do carrinho?' }).then(res => {
      if (res) {
        this.gTagS.removeFromCart(this.cartS.cart.item[index]);
        this.cartS.delItem(this.cartS.cart.item[index]);
      }
    });
  }

  updateQuantidade(value: string, item: IItem) {
    if (Number(value) < 1) {
      value = '1';
    }
    if (Number(value) > item.estoque.estAtual) {
      item.quantidade = item.estoque.estAtual;
      this.modalS.createModal(MdAlertComponent, { title: 'Estoque insuficiente', message: `Só há ${item.estoque.estAtual} unidade(s) deste item em estoque` });
    }
    const index = this.cartS.cart.item.indexOf(item);
    this.cartS.cart.item[index].quantidade = Number(value);
    this.cartS.updateItem(item);
  }

  finalizarCompra() {
    if (this.cep && this.showFrete) {
      const route = this.router.config.find(r => r.path === 'entrega');
      route.data = { cep: this.cep };
    }

    if (this.cartS.cart.valorTotalItems < this.valorMinimo) {
      this.modalS.createModal(MdAlertComponent, { title: 'Valor mínimo não atingido.', message: `Valor mínimo não atingido. Faltam R$ ${(this.valorMinimo - this.cartS.cart.valorTotalItems).toFixed(2)} em produtos para fechar sua compra.` });
      return;
    }

    if (this.userS.user) {
      const user = this.userS.user;
      if (user.pais === 'Brasil') {
        if (!user.cpf && !user.cnpj) {
          this.modalS.createModal(MdAlertComponent, { title: 'Cadastro incompleto.', message: 'Cadastro incompleto, favor entrar na página de cadastro no menu Minha Conta e preencher os dados necessários.'});
          return;
        }
      }

      if ((!user.cep && !user.cobrCep) || (!user.fone && !user.fone2 && !user.fonecelular) || (!user.cobrNumero && !user.numero) || !user.email) {
        this.modalS.createModal(MdAlertComponent, { title: 'Cadastro incompleto.', message: 'Cadastro incompleto, favor entrar na página de cadastro no menu Minha Conta e preencher os dados necessários.'});
        return;
      }
      this.router.navigate(['/entrega']);
      return;
    }

    localStorage.setItem('goCheckout', 'true');
    this.router.navigate(['/cliente/cadastro']);
  }

  getFreteByCep(cep) {
    if (!cep) {
      return;
    }
    ErpInterceptor.useErpHeader = false;
    const validacep = /^[0-9]{8}$/;
    cep = cep.replace(/\D/g, '');
    if (validacep.test(cep)) {
      try {
        this.loadingCep = true;
        const urlViacep = `https://viacep.com.br/ws/${cep}/json/`;
        this.http.get(urlViacep).subscribe((res: any) => {
          if (res.erro) {
            this.modalS.createModal(MdAlertComponent, { title: 'CEP não encontrado.', message: 'Verifique o CEP digitado e tente novamente.' });
            this.loadingCep = false;
            return;
          }
          ErpInterceptor.useErpHeader = true;
          this.cartS.getFreteByCep(cep).subscribe((resp: any) => {
            if (resp.error || resp.data?.error) {
              this.modalS.createModal(MdAlertComponent, { title: 'Entrega não disponível para essa região.', message: 'Infelizmente não existe entregas disponíveis para região informada. Verifique a região de entrega correta no topo da página inicial do site.' });
              this.loadingCep = false;
              this.frete = 0;
              this.showFrete = false;
              return;
            }
            this.loadingCep = false;
            this.showFrete = true;
            if (resp.vFreteGratis) {
              this.vFreteGratis = Number(resp.vFreteGratis);
              if (this.cartS.cart.valorTotalItems >= Number(resp.vFreteGratis)) {
                this.frete = 0;
                return;
              }
            }
            this.frete = resp.data?.vFrete || resp.vFrete;
          });
        });
      } catch (error) {
        this.loadingCep = false;
        console.error(error);
      }
    } else {
      this.loadingCep = false;
      this.modalS.createModal(MdAlertComponent, { title: 'CEP inválido!', message: 'Digite um CEP válido.' });

    }
  }

  openMdNaoSeiCep() {
    this.modalS.createModal(MdNaoseicepComponent).then(res => {
      if (res) {
        this.cep = res;
        this.getFreteByCep(res);
      }
    });
  }

  adicionarCartao() {
    this.modalS.createModal(MdConfirmComponent, { title: 'Adicionar ao carrinho.', message: 'Deseja adicionar cartão especial ao carrinho?' }).then(res => {
      if (res) {
        this.storeS.getItemById(9165).subscribe((cardItem: any) => {
          cardItem.quantidade = 1;
          this.cartS.addItem(cardItem);
        });
      }
    });
  }

  aplicarCupom() {
    if (!this.codCupom) {
      return;
    }
    this.loadingCupom = true;
    try {
      this.pedidoS.cupomDesconto(this.codCupom).subscribe((res: any) => {
        this.loadingCupom = false;
        if (res.error) {
          this.modalS.createModal(MdAlertComponent, {title: 'Cupom de Desconto', message: res.error});
          return;
        }
        this.cartS.cupom = res[0];

        if (res[0].tipo === 'VALOR') {
          this.cartS.desconto = res[0].valor;
        }
        if (res[0].tipo === 'INDICE') {
          this.cartS.desconto = this.cartS.cart.valorTotalItems * res[0].valor / 100;
        }
        this.setValorTotal();
        this.cupomAplicado = true;
      });
    } catch (error) {
      this.loadingCupom = false;
    }
  }

  retirarCupom() {
    this.modalS.createModal(MdConfirmComponent, {title: 'Cupom de Desconto', message: 'Deseja retirar o cupom de desconto?'}).then(confirm => {
      if (confirm) {
        this.codCupom = null;
        this.cartS.cupom = null;
        this.cartS.desconto = 0;
        this.setValorTotal();
        this.cupomAplicado = false;
      }
    });
  }

  private setValorTotal() {
    this.cartS.valorTotal = Math.round(((this.cartS.cart.valorTotalItems + this.cartS.cart.entrega.vFrete - this.cartS.cart.desconto) + Number.EPSILON) * 100) / 100;
  }
}
