import { Injectable } from '@angular/core';

declare let gtag: any;

@Injectable({
  providedIn: 'root'
})
export class GoogleAnalyticsService {

  constructor() { }

  viewItemList(produtos: any, item_list_name: string) {
    window.dataLayer = window.dataLayer || [];
    const produtosLista = [];
    for (const produto of produtos) {
      produtosLista.push({
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda
      });
    }
    window.dataLayer.push({
      event: 'view_item_list',
      ecommerce: {
        items: produtosLista
      }
    });
    gtag('event', 'view_item_list', { items: produtosLista, item_list_name });
  }

  selectItem(produto: any) {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
      event: 'select_content',
      ecommerce: {
        items: [{
          item_name: produto.item_linkWeb.linkWeb.titulo,
          item_id: produto.id,
          price: produto.tabela?.vPromo || produto.tabela?.vVenda
        }]
      }
    });
    gtag('event', 'select_content', {
      items: [{
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda
      }]
    });
  }

  viewItem(produto: any) {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
      event: 'view_item',
      ecommerce: {
        items: [{
          item_name: produto.item_linkWeb.linkWeb.titulo,
          item_id: produto.id,
          price: produto.tabela?.vPromo || produto.tabela?.vVenda
        }]
      }
    });
    gtag('event', 'view_item', {
      items: [{
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda
      }]
    });
  }

  addToCart(produto: any) {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
      event: 'add_to_cart',
      ecommerce: {
        items: [{
          item_name: produto.item_linkWeb.linkWeb.titulo,
          item_id: produto.id,
          price: produto.tabela?.vPromo || produto.tabela?.vVenda
        }]
      }
    });
    gtag('event', 'add_to_cart', {
      currency: 'BRL',
      items: [{
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda
      }],
      value: produto.tabela?.vPromo || produto.tabela?.vVenda
    });
  }

  removeFromCart(produto: any) {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
      event: 'remove_from_cart',
      ecommerce: {
        items: [{
          item_name: produto.item_linkWeb.linkWeb.titulo,
          item_id: produto.id,
          price: produto.tabela?.vPromo || produto.tabela?.vVenda
        }]
      }
    });
    gtag('event', 'remove_from_cart', {
      currency: 'BRL',
      items: [{
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda
      }],
      value: produto.tabela?.vPromo || produto.tabela?.vVenda
    });
  }

  beginCheckout(produtos: any) {
    window.dataLayer = window.dataLayer || [];
    const produtosLista = [];
    for (const produto of produtos) {
      produtosLista.push({
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda,
        quantidade: produto.quantidade
      });
    }
    window.dataLayer.push({
      event: 'begin_checkout',
      ecommerce: {
        items: produtosLista
      }
    });
    gtag('event', 'begin_checkout', {
      currency: 'BRL',
      items: produtosLista
    });
  }

  purchase(cart: any, pedidoWebId: number) {
    window.dataLayer = window.dataLayer || [];
    const produtosLista = [];
    for (const produto of cart.item) {
      produtosLista.push({
        item_name: produto.item_linkWeb.linkWeb.titulo,
        item_id: produto.id,
        price: produto.tabela?.vPromo || produto.tabela?.vVenda,
        quantidade: produto.quantidade
      });
    }
    window.dataLayer.push(
      {
        event: 'purchase',
        ecommerce: {
          purchase: {
            transaction_id: pedidoWebId,
            value: cart?.valorTotal,
            shipping: cart?.entrega?.vFrete,
            currency: 'BRL',
            coupon: cart?.cupom?.cupom || null,
            items: produtosLista
          }
        }
      }
    );
    gtag('event', 'purchase', {
      currency: 'BRL',
      coupon: cart?.cupom?.cupom || null,
      items: produtosLista,
      transaction_id: pedidoWebId,
      value: cart?.valorTotal,
      shipping: cart?.entrega?.vFrete
    });
  }

  login(userId) {
    gtag('event', 'login', {
      event_category: 'engagement',
      value: userId
    });
  }

  search(searchTerm) {
    gtag('event', 'search', {
      event_category: 'engagement',
      search_term: searchTerm
    });
  }
}
