import { Component, OnInit } from '@angular/core';
import { trigger, style, animate, transition} from '@angular/animations';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';
import { MdMeusenderecosComponent } from '../../modais/md-meusenderecos/md-meusenderecos.component';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';
import { MdNaoseicepComponent } from '../../modais/md-naoseicep/md-naoseicep.component';
import { validateDate, formatDateToISO } from '../../../core/Functions';
import { StoreService } from 'src/app/services/store.service';
import { environment } from 'src/environments/environment';

declare var $: any;

@Component({
  selector: 'app-entrega',
  templateUrl: './entrega.component.html',
  styleUrls: ['./entrega.component.scss'],
  animations: [trigger('inOutAnimationMsg', [transition(':enter', [style({ height: 0, opacity: 0 }), animate('0.3s ease-out', style({ height: 285, opacity: 1 }))]), transition(':leave', [style({ height: 285, opacity: 1 }), animate('0.3s ease-in', style({ height: 0, opacity: 0 }))])])]
})
export class EntregaComponent implements OnInit {


  public formGeral: FormGroup;
  public errors = [];
  public today = new Date().toISOString().substring(0, 10);
  private resFrete: number;
  public showDivMetodo = false;
  public showDivPeriodo = false;
  public showRest = false;
  public loadingCep = false;
  public anonimo = true;
  public periodos = [];
  public layout = environment.layout;
  public vFreteGratis = null;
  constructor(private http: HttpClient,
              private fb: FormBuilder,
              private router: Router,
              public cartS: CartService,
              private modalS: ModalService,
              public activatedRoute: ActivatedRoute,
              private storeS: StoreService) { }


  ngOnInit(): void {
    $(() => {
      $('[data-toggle="popover"]').popover();
    });

    if (innerHeight < 850) {
      scrollTo(0, 300);
    }

    this.setupForms();
    ErpInterceptor.useErpHeader = true;
    this.cartS.getTransportePeriodo(this.storeS.layout.emp.id).subscribe((res: any) => {
      if (res.error) {
        this.modalS.createModal(MdAlertComponent, { title: 'Erro ao obter dados do período', message: 'Recarrega a página e tente novamente ou espere alguns minutos. Se o erro persistir, entre em contato com a nossa equipe.' });
        return;
      }
      this.periodos = res.data || res;
    });
  }

  setupForms() {
    this.formGeral = this.fb.group({
      cep: [null],
      data: [null],
      entregaBool: [null],
      entrCadastro: [false],
      periodo: ['7'],
      cartao: [this.layout === 'esaflores'],
      cartao_msg: [null],
      anonimo: [true],
      vFrete: [0],
      cartao_ass: [null],
      send_sms: [false],
      ocultarPedido: [false],
      entrega_nome: [null],
      entrega_sobrenome: [null],
      entrega_cep: [null],
      entrega_logr: [null],
      entrega_numero: [null],
      entrega_complemento: [null],
      entrega_uf: [null],
      entrega_munic: [null],
      entrega_bairro: [null],
      entrega_prox: [null],
      entrega_fone: [null],
      entrega_fone2: [null]
    });

    if (Object.keys(this.cartS.entrega).length > 0 && JSON.parse(localStorage.getItem('cartExpireAt')) > Date.now()) {
      this.formGeral.patchValue(this.cartS.cart.entrega);
      this.buscarCep(this.formGeral.get('cep').value);
      this.showDivMetodo = this.showDivPeriodo = this.showRest = true;
    } else if (this.activatedRoute.snapshot.data.cep) {
      this.formGeral.patchValue({cep: this.activatedRoute.snapshot.data.cep});
      this.buscarCep(this.formGeral.get('cep').value);
    }

    this.formGeral.value.anonimo ?
      this.anonimo = true :
      this.anonimo = false;

    this.resFrete = this.formGeral.value.vFrete;
  }

  buscarCep(cep) {
    if (!cep) {
      return;
    }
    ErpInterceptor.useErpHeader = false;
    const validacep = /^[0-9]{8}$/;
    if (validacep.test(cep)) {
      try {
        this.loadingCep = true;
        this.formGeral.controls.data.disable();
        const urlViacep = `https://viacep.com.br/ws/${cep}/json/`;
        this.http.get(urlViacep).subscribe((res: any) => {
          if (res.erro) {
            this.formGeral.patchValue({cep: ''});
            this.modalS.createModal(MdAlertComponent, {title: 'CEP não encontrado.', message: 'Verifique o CEP digitado e tente novamente.'});
            this.loadingCep = false;
            return;
          }

          this.formGeral.patchValue({
            cep,
            entrega_cep: cep,
            entrega_logr: res.logradouro,
            entrega_bairro: res.bairro,
            entrega_munic: res.localidade,
            entrega_uf: res.uf,
            entrega_complemento: res.complemento
          });
          this.getFreteByCep(cep);
        });
      } catch (error) {
        this.loadingCep = false;
        this.formGeral.controls.data.enable();
      }
    } else {
      this.formGeral.patchValue({cep: ''});
      this.modalS.createModal(MdAlertComponent, {title: 'CEP inválido!', message: 'Digite um CEP válido.'});
    }
  }

  getFreteByCep(cep) {
    ErpInterceptor.useErpHeader = true;
    this.cartS.getFreteByCep(cep).subscribe((res: any) => {
      this.loadingCep = false;
      this.formGeral.controls.data.enable();
      if (res.error || res.data?.error) {
        this.modalS.createModal(MdAlertComponent, {title: 'Entrega não disponível para essa região.', message: 'Infelizmente não existe entregas disponíveis para região informada. Verifique a região de entrega correta no topo da página inicial do site.'});
        this.showRest = false;
        this.showDivPeriodo = false;
        this.showDivMetodo = false;
        this.formGeral.patchValue({vFrete: 0});
        return;
      }
      this.showDivMetodo = true;
      if (this.formGeral.value.entregaBool) {
        this.showDivPeriodo = true;
        this.showRest = true;
      }
      if (this.formGeral.value.entregaBool === false) {
        this.formGeral.patchValue({vFrete: 0});
        this.resFrete = 0;
        this.showDivPeriodo = true;
        this.showRest = true;
        return;
      }
      if (res.vFreteGratis) {
        this.vFreteGratis = Number(res.vFreteGratis);
        if (this.cartS.cart.valorTotalItems >= res.vFreteGratis) {
          this.formGeral.patchValue({vFrete: 0});
          this.resFrete = 0;
          return;
        }
      }
      this.formGeral.patchValue({vFrete: res.data?.vFrete || res.vFrete});
      this.resFrete = res.data?.vFrete || res.vFrete;
    });
  }

  changeRadioAnonimo($event) {
    $event.target.id === 'radioAnonimo' ?
      this.anonimo = true :
      this.anonimo = false;
    this.formGeral.patchValue({cartao_ass: null});
  }

  changePeriodo() {
    this.showRest = true;
  }

  filterPeriodos() {
    if (this.storeS.layout.emp.id === 1) {
      // return this.periodos.filter(transpId => {
      //   if (this.formGeral.value.entregaBool) {
      //     return transpId.transporte_id === 8 && transpId.id === 7;
      //   } else {
      //     return transpId.transporte_id === 1;
      //   }
      // });
      if (this.layout === 'esalflores') {
        return this.periodos.filter(transpId => transpId.transporte_id === (this.formGeral.value.entregaBool ? 8 : 1));
      }
      if (this.layout === 'esalpet') {
        return this.periodos.filter(transpId => transpId.transporte_id === (this.formGeral.value.entregaBool ? 2 : 1));
      }
    }
    return this.periodos;
  }

  changeMetodoEntrega() {
    this.formGeral.value.entregaBool ? this.formGeral.patchValue({vFrete: this.resFrete}) : this.formGeral.patchValue({vFrete: 0});
    this.errors = [];
    this.showDivPeriodo = true;
    if (this.formGeral.value.periodo) {
      this.showRest = true;
    }
    this.formGeral.patchValue({periodo: null});
  }

  checkForm() {
    this.errors = [];

    if (!validateDate(this.formGeral.value.data)) {
      this.errors.push('Data com formato inválido! Selecione a data de entrega novamente, se o problema persistir, favor entrar em contato com a nossa equipe.');
    }

    if (!this.formGeral.value.cep) {
      this.errors.push('Campo CEP da região obrigatório');
    }

    if (!this.formGeral.value.data) {
      this.errors.push('Campo DATA obrigatório.');
    } else {
      const calendarDate = new Date(this.formGeral.value.data + 'T23:59');
      const today = new Date();
      if (this.formGeral.value.periodo === '13') {
        // if (calendarDate.getDate() !== 21 && calendarDate.getDay() !== 0) {
        if (calendarDate.getDay() !== 0) {
          this.errors.push('Período de "Domingos e Feriados" só pode ser escolhido nos domingos ou feriados');
        }
      }

      if (calendarDate.getDay() === 0 && this.formGeral.value.periodo !== '13' && this.formGeral.value.entregaBool) {
        this.errors.push('Para entrega aos domingos, favor escolher o período de entrega "Domingos e Feriados (9h as 14h)".');
      }

      if (calendarDate < today) {
        this.errors.push('Data selecionada precisa ser maior que a atual.');
      }
      if (calendarDate.getDate() + calendarDate.getMonth() === today.getDate() + today.getMonth()) {
        // if (this.formGeral.value.periodo === '13') {
        //   this.errors.push('Horário corte de domingos e feriados (11h) atingido. Favor escolher uma data posterior a atual.');
        // }
        if (today.getDay() === 0) {
          if (today.getHours() >= 12) {
            this.errors.push('Horário de corte dos domingos (12h) atingido, favor escolher uma data posterior a atual.');
          }
        } else {
          // tslint:disable-next-line:triple-equals
          if ((today.getHours() >= 10 || (today.getHours() === 9 && today.getMinutes() >= 30)) && (this.formGeral.value.periodo == 12 || this.formGeral.value.periodo == 1)) {
            this.errors.push('Horário de corte (09h30min) atingido para o período da manhã, favor escolher o período da tarde ou uma data posterior a atual.');
          }
          else if (today.getHours() >= 16) {
            this.errors.push('Horário de corte (16h) atingido, favor escolher uma data posterior a atual.');
          }
        }
      }
    }

    if (this.formGeral.value.entregaBool) {
      if (!this.formGeral.value.entrega_nome) {
        this.errors.push('Campo NOME de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_sobrenome) {
        this.errors.push('Campo SOBRENOME de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_cep) {
        this.errors.push('Campo CEP de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_logr) {
        this.errors.push('Campo ENDEREÇO de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_numero) {
        this.errors.push('Campo NÚMERO de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_uf) {
        this.errors.push('Campo UF de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_munic) {
        this.errors.push('Campo CIDADE de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_bairro) {
        this.errors.push('Campo BAIRRO de entrega obrigatório.');
      }
      if (!this.formGeral.value.entrega_fone) {
        this.errors.push('Campo TELEFONE de entrega obrigatório.');
      }
    } else {
      if (!this.formGeral.value.entrega_nome) {
        this.errors.push('Campo NOME de retirada obrigatório.');
      }
      if (!this.formGeral.value.entrega_sobrenome) {
        this.errors.push('Campo SOBRENOME de retirada obrigatório.');
      }
    }

    if (!this.formGeral.value.periodo) {
      this.errors.push('Selecione o período de entrega.');
    }

    if (this.formGeral.value.cartao && this.layout === 'esalflores') {
      if (!this.formGeral.value.cartao_msg) {
        this.errors.push('Escreva uma mensagem para o cartão de mensagem.');
      }
    }
  }

  clearEntrega() {
    this.formGeral.patchValue({
      vFrete: 0,
      entrega_cep: null,
      entrega_logr: null,
      entrega_numero: null,
      entrega_complemento: null,
      entrega_uf: null,
      entrega_munic: null,
      entrega_bairro: null,
      entrega_prox: null,
      entrega_fone: null,
      entrega_fone2: null
    });
  }

  clearRetirada() {
    this.formGeral.patchValue({
      vFrete: this.resFrete
    });
  }

  modalMeusEnderecos() {
    this.modalS.createModal(MdMeusenderecosComponent).then(res => {
      if (res) {
        this.buscarCep(res.cep);
        this.formGeral.patchValue({
          cep: res.cep,
          entrega_cep: res.cep,
          entrega_logr: res.logradouro,
          entrega_numero: res.numero,
          entrega_complemento: res.complemento,
          entrega_uf: res.uf,
          entrega_munic: res.municipio,
          entrega_bairro: res.bairro
        });
        document.getElementById('inputData').focus();
      }
    });
  }

  openMdNaoSeiCep() {
    this.modalS.createModal(MdNaoseicepComponent).then(res => {
      if (res) {
        this.formGeral.patchValue({cep: res});
        this.buscarCep(res);
      }
    });
  }

  goToPagamento() {
    if (!validateDate(this.formGeral.value.data)) {
      this.formGeral.patchValue({ data: formatDateToISO(this.formGeral.value.data)});
    }
    this.checkForm();
    if (this.formGeral.invalid || this.errors.length > 0) {
      return;
    }

    this.formGeral.value.entregaBool ? this.clearRetirada() : this.clearEntrega();

    // localStorage.setItem('form-entrega', JSON.stringify(this.formGeral.value));
    this.cartS.entrega = this.formGeral.value;
    this.router.navigate(['/pagamento']);
  }
}
