  public paises = listaPaises();
  public cpfcnpjValido = true;
  public loading = false;
  public showErrors = false;
  public firstLogin = false;
  public formCadastro: FormGroup;
  public formCobranca: FormGroup;
  public valorMinimo = 1;
  formGeral: FormGroup;
  dadosCC: FormGroup;
  dataEntrega: string;
  timerPicPay: any;
  public codCupom;
  public qrCodePicPay: string;
  public qrCodePix: string;
  public linkPix: string;
  private timerPix: any;
  public msgSucessoCopy = false;
  public picpayTabActive = null;
  public loadingCupom = false;
  nsu = null;
  public disableFinalizarBtn = false;
  public errors = [];
  private bGetStatusPix = true;
  private bGetStatusPicPay = true;
  public bPayPalBtn = true;
  public layout = environment.layout;
  public cupomAplicado = false;
  public cpfTitular = true;
  public fatura = true;
  public salvarCartao = true;
  public responseData:any;
