    public userS: UserService,
    public cartS: CartService,
    private router: Router,
    private picPayS: PicpayService,
    private pedidoS: PedidoService,
    private mundipaggS: MundipaggService,
    private paypalS: PaypalService,
    private modalS: ModalService,
    private bancoBrasilS: BancobrasilService,
