import { ComponentFactoryResolver, Injectable, OnInit, ViewContainerRef } from '@angular/core';
import { StoreService } from './store.service';

@Injectable({
  providedIn: 'root'
})
export class ModalService {
  public bootstrapModal: ViewContainerRef;

  constructor(private componentFactoryResolver: ComponentFactoryResolver) {}

  createModal(component, data = {}): Promise<any> {
    // Caso já tenha um modal aberto, ignorar

    return new Promise((resolve) => {
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);
      const viewContainerRef = this.bootstrapModal;
      const componentRef: any = viewContainerRef.createComponent(componentFactory);
      const jqInstance: any = $(componentRef.location.nativeElement.querySelector('.modal'));

      if (viewContainerRef.length > 3) {
        viewContainerRef.clear();
      }

      componentRef.instance.data = data;
      componentRef.instance.modal = {
        close: d => {
          jqInstance.modal('hide');
        }
      };

      // Events

      if (componentRef.instance.onShownBsModal) {
        jqInstance.on('shown.bs.modal', componentRef.instance.onShownBsModal);
      }

      jqInstance.on('hide.bs.modal', () => {
        viewContainerRef.remove();
        resolve(componentRef.instance.resolve);
      });

      // Show modal
      jqInstance.modal({backdrop: 'static'});

    });
  }


}

